<template>
	<div id="chat">
		<div class="main">
			<chat-log v-for="(chatData,index) in chatDatas" :userId="userId" :chatData="chatData" :key="'log'+index"></chat-log>
		</div>
		<div class="online">
			<div>
				<button class="btn-back" onclick="javascript:window.location.href='#/'"> </button>
			</div>
			<div class="current_online"></div>
		</div>
		<div class="input">
			<form>
				<div class="form-input">
					<div class="toolbar">
						<select name="fontFamily" @change="familyChange">
							<option value="" selected="selected"></option>
							<option value=""></option>
							<option value=""></option>
							<option value=""></option>
						</select>
						<select name="fontSize" @change="sizeChange">
							<option value="14px"></option>
							<option value="20px" selected="selected"></option>
							<option value="30px"></option>
							<option value="40px"></option>
						</select>
						<button id="picker" onclick="javascript:event.preventDefault()"></button>
					</div>
					<textarea type="text" name="content" @keypress="enterHandle" :disabled="isBanned" />
				</div>
				<div class="form-btn">
					<button type="submit" @click="chatSub" v-if="!isBanned"></button>
					<button type="submit" @click="chatSub"v-if="isBanned" :disabled="isBanned" style="color: red;"></button>
				</div>
			</form>
		</div>
	</div>
</template>

<script>
	module.exports = {
		data: function() {
			return {
				userId: '',
				chatDatas: [],
				isScroll: false,
				admin: false,
				isBanned: false
			}
		},
		components: {
			chatLog: require('../components/chatLog').default
		},
		methods: {
			enterHandle: function(event) {
				let ev = event || window.event;
				if(ev.keyCode == 13) {
					this.chatSub(ev);
				}
			},
			familyChange: function() {
				let ev = event || window.event;
				$('textarea').css('font-family', ev.target.value)
			},
			sizeChange: function() {
				let ev = event || window.event;
				$('textarea').css('font-size', ev.target.value)
			},
			chatSub: function(event) {
				let vue = this;
				let ev = event || window.event;
				let subData = {
					fontSize: $('select[name="fontSize"]')[0].value,
					fontFamily: $('select[name="fontFamily"]')[0].value,
					fontColor: $('.colpick_hex_field input')[0].value,
					content: $('textarea')[0].value
				}
				ev.preventDefault();
				if(subData.content) {
					$.ajax({
						type: "POST",
						url: "http://localhost:3000/chatMsg",
						xhrFields: {
							withCredentials: true
						},
						data: subData,
						crossDomain: true,
						success: function(res) {
							vue.getChatData();
							$('textarea')[0].value = '';
						}
					})

				}
			},
			getChatData: function() {
				let vue = this;
				$.ajax({
					type: "GET",
					url: "http://localhost:3000/getChatContent",
					xhrFields: {
						withCredentials: true
					},
					crossDomain: true,
					success: function(res) {
						res.data.sort(function(a, b) {
							return a.log.Date > b.log.Date ? 1 : -1
						})
						vue.chatDatas = res.data;
						vue.isBanned = res.isBanned;
					}
				})
			}
		},
		mounted: function() {
			let vue = this;
			$('#app').css({
				'background': 'none',
				'background-image': 'url(static/img/battle.jpg)',
				'background-size': '140% 110%',
				'background-position': '35% 35%'
			})
			$.ajax({
				type: "GET",
				url: "http://localhost:3000/toChat",
				xhrFields: {
					withCredentials: true
				},
				crossDomain: true,
				success: function(res) {
					console.log(res)
					let avatar_data = [];
					if(res.msg != 'noUser') {
						vue.userId = res.userId;
						vue.isBanned = res.isBanned;
						res.msg.sort(function(a, b) {
							return a.isOnline < b.isOnline ? 1 : -1
						})
						for(let i = 0; i < res.msg.length; i++) {
							if(res.msg[i].avatar) {
								if(res.msg[i].lastSpeakDate) {
									if(res.msg[i].isOnline) {
										$('.online').append('<div class="user-block"><span class="online-state-online"></span><div class="chat-avatar-cover"><img data-index="' + i + '" src="/static/avatar/' + res.msg[i].avatar.avatar_src + '" /></div><div>' + res.msg[i].realname + '</div><span class="last-speak">' + res.msg[i].lastSpeakDate.split("T")[0] + ' ' + res.msg[i].lastSpeakDate.split("T")[1].split(".")[0] + '</span></div>');
									} else {
										$('.online').append('<div class="user-block"><span class="online-state-offline"></span><div class="chat-avatar-cover"><img data-index="' + i + '" src="/static/avatar/' + res.msg[i].avatar.avatar_src + '" /></div><div>' + res.msg[i].realname + '</div><span class="last-speak">' + res.msg[i].lastSpeakDate.split("T")[0] + ' ' + res.msg[i].lastSpeakDate.split("T")[1].split(".")[0] + '</span></div>');
									}
								} else {
									if(res.msg[i].isOnline) {
										$('.online').append('<div class="user-block"><span class="online-state-online"></span><div class="chat-avatar-cover"><img data-index="' + i + '" src="/static/avatar/' + res.msg[i].avatar.avatar_src + '" /></div><div>' + res.msg[i].realname + '</div><span class="last-speak"></span></div>');
									} else {
										$('.online').append('<div class="user-block"><span class="online-state-offline"></span><div class="chat-avatar-cover"><img data-index="' + i + '" src="/static/avatar/' + res.msg[i].avatar.avatar_src + '" /></div><div>' + res.msg[i].realname + '</div><span class="last-speak"></span></div>');
									}
								}
								avatar_data[i] = JSON.parse(res.msg[i].avatar.avatar_data);
							} else {
								if(res.msg[i].lastSpeakDate) {
									if(res.msg[i].isOnline) {
										$('.online').append('<div class="user-block"><span class="online-state-online"></span><div class="chat-avatar-cover"><img data-index="' + i + '" src="/static/img/default.jpg" style="width:40px; vertical-align:top;"/></div><div>' + res.msg[i].realname + '</div><span class="last-speak">' + res.msg[i].lastSpeakDate.split("T")[0] + ' ' + res.msg[i].lastSpeakDate.split("T")[1].split(".")[0] + '</span></div>');
									} else {
										$('.online').append('<div class="user-block"><span class="online-state-offline"></span><div class="chat-avatar-cover"><img data-index="' + i + '" src="/static/img/default.jpg" style="width:40px; vertical-align:top;"/></div><div>' + res.msg[i].realname + '</div><span class="last-speak">' + res.msg[i].lastSpeakDate.split("T")[0] + ' ' + res.msg[i].lastSpeakDate.split("T")[1].split(".")[0] + '</span></div>');
									}
								} else {
									if(res.msg[i].isOnline) {
										$('.online').append('<div class="user-block"><span class="online-state-online"></span><div class="chat-avatar-cover"><img data-index="' + i + '" src="/static/img/default.jpg" style="width:40px; vertical-align:top;"/></div><div>' + res.msg[i].realname + '</div><span class="last-speak"></span></div>');
									} else {
										$('.online').append('<div class="user-block"><span class="online-state-offline"></span><div class="chat-avatar-cover"><img data-index="' + i + '" src="/static/img/default.jpg" style="width:40px; vertical-align:top;"/></div><div>' + res.msg[i].realname + '</div><span class="last-speak"></span></div>');
									}
								}
								avatar_data[i] = null;
							}

						}
						for(let j = 0; j < res.msg.length; j++) {
							if(avatar_data[j]) {
								$('.online img[data-index="' + j + '"]').on('load', function() {
									//
									var imgWidth = $(this)[0].width;
									//
									$(this).css('width', (imgWidth / avatar_data[j].width) * 40 + 'px');
									$(this).css('margin-left', -(avatar_data[j].x / avatar_data[j].width) * 40 + 'px');
									$(this).css('margin-top', -(avatar_data[j].y / avatar_data[j].width) * 40 + 'px');

								})
							}
						}

					}
					$('.current_online').text('' + $('.online-state-online').length + '')
					window.getDataIntv = setInterval(function() {
						vue.getChatData();
					}, 500)
				}
			})

			$('#picker').colpick({
				colorScheme: 'dark',
				submit: false,
				color: 'ffffff',
				layout: 'hex',
				onChange: function(hsb, hex, rgb, el, bySetColor) {
					$(el).css('border-color', '#' + hex);
					$('textarea').css('color', '#' + hex);
				}
			})
			window.scrollIntv = setInterval(function() {
				if(!vue.isScroll) {
					$('.main').scrollTop($('.main')[0].scrollHeight);
				}
			}, 500)
			$('.main').on('scroll', function() {
				if(($('.main')[0].scrollHeight - ($('.main')[0].clientHeight + $('.main').scrollTop())) > 100) {
					vue.isScroll = true;
				} else if(($('.main')[0].scrollHeight - ($('.main')[0].clientHeight + $('.main').scrollTop())) < 100 && ($('.main')[0].scrollHeight - ($('.main')[0].clientHeight + $('.main').scrollTop())) > 0) {
					vue.isScroll = false;
				}
			})
		},
		destroyed: function() {
			clearInterval(scrollIntv);
			clearInterval(getDataIntv);
		}

	}
</script>

<style scoped>
	#chat {
		width: 100%;
		height: 100%;
		position: relative;
	}
	
	.online {
		height: 100%;
		width: 250px;
		background-color: red;
		position: absolute;
		left: 0;
		top: 0;
		border: 1px solid #DFDFDF;
		border-right: none;
		background-color: rgba(0, 0, 0, 0.6);
		overflow-x: hidden;
		overflow-y: auto;
	}
	
	.main {
		width: calc(100% - 250px);
		height: calc(100% - 200px);
		background-color: rgba(0, 0, 0, 0.8);
		position: absolute;
		top: 0;
		right: 0;
		border: 1px solid #DFDFDF;
		padding: 60px 10px;
		overflow: auto;
	}
	
	.input {
		width: calc(100% - 250px);
		height: 200px;
		background-color: rgba(0, 0, 0, 0.4);
		position: absolute;
		bottom: 0;
		right: 0;
		border: 1px solid #DFDFDF;
		border-top: none;
	}
	
	.input form>div {
		float: left;
	}
	
	form {
		zoom: 1;
		height: 100%;
		width: 100%;
	}
	
	form:after {
		content: '.';
		height: 0;
		display: block;
		clear: both;
		visibility: hidden;
	}
	
	.form-input {
		width: 85%;
		height: 100%;
	}
	
	.form-btn {
		width: 15%;
		height: 100%;
	}
	
	.form-btn button {
		height: 100%;
		width: 100%;
		border: none;
		outline: none;
		font-size: 50px;
		color: white;
		transition-duration: 0.2s;
		border-left: 1px solid white;
		background-color: rgba(255, 255, 255, 0.2);
	}
	
	.form-btn button:hover {
		background-color: rgba(255, 255, 255, 0.8);
		color: grey;
	}
	
	.toolbar {
		width: 100%;
		height: 50px;
		background-color: rgba(0, 0, 0, 0.4);
		border-bottom: 1px solid #DFDFDF;
		font-size: 24px;
		padding: 5px 0;
	}
	
	form textarea {
		display: block;
		resize: none;
		width: 100%;
		height: calc(100% - 50px);
		background: none;
		color: white;
		font-size: 20px;
	}
	
	.toolbar select {
		background: none;
		color: white;
		height: 100%;
		outline: none;
		border: none;
	}
	
	.toolbar option {
		background: black;
		border: none;
	}
	
	#picker {
		height: 100%;
		border: none;
		outline: none;
		color: white;
		background: none;
		border-right: 40px solid white;
	}
	
	.current_online {
		width: 100%;
		font-size: 20px;
		color: yellow;
		text-align: center;
	}
	
	.btn-back {
		width: 100%;
		height: 50px;
		background: none;
		border: none;
		outline: none;
		border-bottom: 1px solid #DFDFDF;
		font-size: 28px;
		color: #DFDFDF;
	}
</style>


