<template>
	<div id="exam">
		<div v-if="diffDetail.content" class="diff-detail animated fadeIn">
			<div class="detail-panel">
				<div>{{diffDetail.content}}</div>
				<div>{{diffDetail.quantity}}</div>
				<button @click="diffDetail.content='',diffDetail.quantity=0"></button>
			</div>
		</div>
		<div id="difficulty-choose" v-if="!gameFlag">
			<select name="difficulty">
				<option value="0"></option>
			</select>
			<div class="diff-btn">
				<button v-if="menuIndex[0]" @click="diffChoose" type="button" class="animated fadeInLeft difficulty_btn1"></button>
				<button v-if="menuIndex[1]" @click="diffView" type="button" class="animated fadeInLeft difficulty_btn2"></button>
				<button v-if="menuIndex[2]" @click="ruleFlag=true" type="button" class="animated fadeInLeft"></button>
				<button v-if="menuIndex[3]" @click="rank" type="button" class="animated fadeInLeft"></button>
				<button v-if="menuIndex[4]" @click="back" type="button" class="animated fadeInLeft"></button>
			</div>

		</div>
		<div v-if="ruleFlag" id="user-guide" class="animated fadeIn">
			<div>
				<h3></h3>
				<ol>
					<li></li>
					<li></li>
					<li> <kbd>ENTER</kbd> </li>
					<li></li>
					<li>"53""3/5", <kbd>SHIFT</kbd>  <kbd>/</kbd> </li>
					<li>"231""2'1/3" <kbd>ENTER</kbd>  <kbd>'</kbd> </li>
					<li></li>
					<li></li>
				</ol>
				<button type="button" @click="ruleFlag=false"></button>
			</div>
		</div>
		<ul id="question_info">
			<li><i class="fa fa-clock-o fa-lg fa-fw"></i><span class="timer">{{timer.hou}}:{{timer.min}}:{{timer.sec}}</span></li>
			<li class="right_num animated"><i class="fa fa-check fa-lg fa-fw"></i><span>0</span></li>
			<li class="wrong_num animated"><i class="fa fa-close fa-lg fa-fw"></i><span>0</span></li>
			<li style="cursor: pointer; color: yellow;" v-if="gameFlag" @click="backToMenu"><i class="fa fa-chevron-left fa-lg fa-fw"></i>()</li>
		</ul>

		<question :key="'ques'+index" v-show="cur_question.index==index" :index="cur_question" :indexf="index" :questionObj="question_list" :timer="timer" v-for="(question_list,index) in question_lists"></question>

	</div>
</template>

<script>
	module.exports = {
		data: function() {
			return {
				gameFlag: false,
				ruleFlag: false,
				menuIndex: [],
				cur_question: {
					index: 0
				},
				diffDetail: {
					content: '',
					quantity: 0
				},
				question_lists: [],
				timer: {
					sec: '0' + 0,
					min: '0' + 0,
					hou: '0' + 0,
					startTimer: function() {
						this.sec = '0' + 0, this.min = '0' + 0, this.hou = '0' + 0;
						this.sec = '0' + 1;
						setTimer = setInterval(function() {
							this.sec++;
							if(this.sec == 60) {
								this.sec = '0' + 0;
								this.min++;
							}
							if(this.min == 60) {
								this.min = '0' + 0;
								this.hou++;
							}
							this.hou < 10 && this.hou.toString().length < 2 ? this.hou = '0' + this.hou : this.hou = this.hou;
							this.min < 10 && this.min.toString().length < 2 ? this.min = '0' + this.min : this.min = this.min;
							this.sec < 10 && this.sec.toString().length < 2 ? this.sec = '0' + this.sec : this.sec = this.sec;
						}.bind(this), 1000);
					},
					stopTimer: function() {
						clearInterval(setTimer);
					}
				}
			}
		},
		components: {
			question: require('../components/question.vue').default
		},
		methods: {
			backToMenu: function() {
				this.cur_question.index = 0;
				this.question_lists = [];
				this.menuFun();
				$('.question_collec').text('');
				this.timer.stopTimer();
				this.timer.sec = '00';
				this.timer.min = '00';
				this.timer.hou = '00';
				this.gameFlag = false;
				$.get('http://localhost:3000/difficulty', function(res) {
					$.each(res, function(index, item) {
						$('#difficulty-choose select').append('<option value="' + item + '">' + item + '</option>');
					})
				})
			},
			menuFun: function() {
				let a = this.menuIndex
				a.push(1);
				setTimeout(function() {
					a.push(1);
				}, 100)
				setTimeout(function() {
					a.push(1);
				}, 200)
				setTimeout(function() {
					a.push(1);
				}, 300)
				setTimeout(function() {
					a.push(1);
				}, 400)
				setTimeout(function() {
					$('.diff-btn button').removeClass('fadeInLeft');
				}, 900)
			},
			menuOutFun:function(){
				$('.diff-btn button:eq(4)').addClass('fadeOutLeft');
				setTimeout(function() {
					$('.diff-btn button:eq(3)').addClass('fadeOutLeft');
				}, 100)
				setTimeout(function() {
					$('.diff-btn button:eq(2)').addClass('fadeOutLeft');
				}, 200)
				setTimeout(function() {
					$('.diff-btn button:eq(1)').addClass('fadeOutLeft');
				}, 300)
				setTimeout(function() {
					$('.diff-btn button:eq(0)').addClass('fadeOutLeft');
				}, 400)
			},
			rank:function(){
				this.menuOutFun();
				setTimeout(function() {
					window.location.href = '#/exam/rank'
				}, 900)
			},
			back: function() {
				this.menuOutFun();
				setTimeout(function() {
					window.location.href = '#/'
				}, 900)
			},
			diffView: function() {
				let diffName = $('#difficulty-choose select').val();
				let diffDetail = this.diffDetail;
				if(diffName != 0) {
					$.get('http://localhost:3000/difficulty_info', {
						diffName: diffName
					}, function(res) {
						diffDetail.content = res.msg.content;
						diffDetail.quantity = res.msg.quantity;
					})
				}
			},
			diffChoose: function() {
				let vue = this;
				var diffName = $('#difficulty-choose select').val();
				if(diffName != 0) {
					$.get('http://localhost:3000/difficulty_detail', {
						diffName: diffName
					}, function(res) {
						vue.menuOutFun();
						setTimeout(function() {
							vue.gameFlag = true;
							vue.menuIndex = [];
							vue.startTest(res.msg, diffName);
						}, 900)

					})
				}
			},
			startTest: function(detail, diffName) {
				if(detail.extraSetting == 'none') {
					for(let i = 1; i <= detail.quantity; i++) {
						document.getElementsByClassName('question_collec')[0].innerHTML += '<button class="" data-index="' + i + '">' + i + '</button>'
						if(i == detail.quantity) {
							this.question_lists.push({
								index: i,
								equ: this.createInteger(detail.numRange, detail.operator),
								diffName: diffName,
								finishFlag: true
							});

						} else {
							this.question_lists.push({
								index: i,
								equ: this.createInteger(detail.numRange, detail.operator),
								diffName: diffName,
								finishFlag: false
							});
						}

					}
				} else if(detail.extraSetting == 'decimal') {
					//
					for(let i = 1; i <= detail.quantity; i++) {
						document.getElementsByClassName('question_collec')[0].innerHTML += '<button class="" data-index="' + i + '">' + i + '</button>'
						if(i == detail.quantity) {
							this.question_lists.push({
								index: i,
								equ: this.createDecimal(detail.numRange, detail.operator),
								diffName: diffName,
								finishFlag: true
							});
						} else {
							this.question_lists.push({
								index: i,
								equ: this.createDecimal(detail.numRange, detail.operator),
								diffName: diffName,
								finishFlag: false
							});
						}

					}
				} else if(detail.extraSetting == 'fraction') {
					//
					for(let i = 1; i <= detail.quantity; i++) {
						document.getElementsByClassName('question_collec')[0].innerHTML += '<button class="" data-index="' + i + '">' + i + '</button>'
						if(i == detail.quantity) {
							this.question_lists.push({
								index: i,
								equ: this.createFraction(detail.numRange, detail.operator),
								diffName: diffName,
								finishFlag: true
							});
						} else {
							this.question_lists.push({
								index: i,
								equ: this.createFraction(detail.numRange, detail.operator),
								diffName: diffName,
								finishFlag: false
							});
						}

					}
				}

				this.timer.startTimer();
				let i = this.cur_question
				$('.question_collec button').on('click', function() {
					i.index = $(this).text() - 1;
				})
			},
			//
			createInteger: function(numRange, operator) {
				var r = numRange;
				//1~3
				var ope_count = Math.round(Math.random() * 2) + 1;
				//
				var bra_position = Math.round(Math.random() * ope_count);
				//
				var number = [];
				//
				for(let i = 0; i < ope_count + 1; i++) {
					number.push(Math.ceil(Math.random() * r));
				}
				//
				if(ope_count == 1) {
					var equ = number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift();
				} else if(ope_count == 2) {
					switch(bra_position) {
						case 0:
							var equ = number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift();
							break;
						case 1:
							var equ = '(' + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + ')' + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift();
							break;
						case 2:
							var equ = number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + '(' + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + ')';
							break
					}

				} else if(ope_count == 3) {
					switch(bra_position) {
						case 0:
							var equ = number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift();
							break;
						case 1:
							var equ = '(' + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + ')' + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift();
							break;
						case 2:
							var equ = number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + '(' + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + ')' + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift();
							break;
						case 3:
							var equ = number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + '(' + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + ')';
							break;
					}

				}
				return equ;
			},
			//
			createDecimal: function(numRange, operator) {
				var r = numRange;
				//1~3
				var ope_count = Math.round(Math.random() * 2) + 1;
				//
				var bra_position = Math.round(Math.random() * ope_count);
				//
				var number = [];
				//
				for(let i = 0; i < ope_count + 1; i++) {
					if(Math.random() < 0.4) {
						number.push(Math.ceil(Math.random() * r));
					} else {
						number.push((Math.random() * r).toFixed(2));
					}
				}
				//
				if(ope_count == 1) {
					var equ = number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift();
				} else if(ope_count == 2) {
					switch(bra_position) {
						case 0:
							var equ = number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift();
							break;
						case 1:
							var equ = '(' + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + ')' + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift();
							break;
						case 2:
							var equ = number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + '(' + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + ')';
							break
					}

				} else if(ope_count == 3) {
					switch(bra_position) {
						case 0:
							var equ = number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift();
							break;
						case 1:
							var equ = '(' + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + ')' + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift();
							break;
						case 2:
							var equ = number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + '(' + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + ')' + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift();
							break;
						case 3:
							var equ = number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + '(' + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + ')';
							break;
					}

				}
				return equ;
			},

			//
			createFraction: function(numRange, operator) {
				var r = numRange;
				//1~3
				var ope_count = Math.round(Math.random() * 2) + 1;
				//
				var bra_position = Math.round(Math.random() * ope_count);
				//
				var number = [];
				//
				for(let i = 0; i < ope_count + 1; i++) {
					if(Math.random() < 0.35) {
						number.push(Math.ceil(Math.random() * r));
					} else {
						var fraction = '(' + Math.ceil(Math.random() * r) + '/' + Math.ceil(Math.random() * r) + ')'
						number.push(fraction);
					}
				}
				//
				if(ope_count == 1) {
					var equ = number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift();
				} else if(ope_count == 2) {
					switch(bra_position) {
						case 0:
							var equ = number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift();
							break;
						case 1:
							var equ = '(' + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + ')' + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift();
							break;
						case 2:
							var equ = number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + '(' + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + ')';
							break
					}

				} else if(ope_count == 3) {
					switch(bra_position) {
						case 0:
							var equ = number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift();
							break;
						case 1:
							var equ = '(' + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + ')' + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift();
							break;
						case 2:
							var equ = number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + '(' + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + ')' + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift();
							break;
						case 3:
							var equ = number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + '(' + number.shift() + operator[Math.round(Math.random() * (operator.length - 1))] + number.shift() + ')';
							break;
					}

				}
				return equ;

			}

		},
		mounted: function() {
			$.ajax({
				type: "GET",
				url: "http://localhost:3000/isLogin",
				xhrFields: {
					withCredentials: true
				},
				crossDomain: true,
				success: function(res) {
					if(!res.data) {
					alert('');
					window.location.href='#/';
				}
				}
			})
			this.menuFun();
			$.get('http://localhost:3000/difficulty', function(res) {
				$.each(res, function(index, item) {
					$('#difficulty-choose select').append('<option value="' + item + '">' + item + '</option>');
				})
			})

			$('#app').css({
				'background': 'none',
				'background-color': 'brown',
				'background-image': 'url(static/img/digit.png)',
				'background-size': 'auto !important'
			})

		},
		destroyed: function() {
			$('.question_collec').text('');
		}
	}
</script>

<style scoped>
	#exam {
		position: relative;
		height: 100%;
		overflow: hidden;
	}
	
	.diff-detail {
		position: absolute;
		height: 300px;
		width: 400px;
		left: calc(50% - 200px);
		top: calc(50% - 150px);
		background-image: url(../assets/img/login_border.png);
		background-size: 130% 130%;
		padding: 10px;
		background-repeat: no-repeat;
		background-position: 39% 45%;
		z-index: 99;
	}
	
	.diff-detail .detail-panel {
		width: 365px;
		margin: 28px auto;
		background-color: rgba(0, 0, 0, 0.7);
		height: 232px;
		color: #DFDFDF;
		font-size: 26px;
		padding: 10px 20px;
		line-height: 50px;
		font-family: "";
		position: relative;
	}
	
	.diff-detail .detail-panel button {
		position: absolute;
		display: block;
		width: 80px;
		background: none;
		outline: none;
		height: 40px;
		padding: 0;
		line-height: 40px;
		font-size: 20px;
		font-family: "";
		border: none;
		transition-duration: 0.3s;
		bottom: 5px;
		left: calc(50% - 40px);
	}
	
	.diff-detail .detail-panel button:hover {
		color: gold;
	}
	
	#user-guide {
		position: absolute;
		left: calc(50% - 200px);
		width: 400px;
		background-image: url(../assets/img/rule_border.png);
		background-size: 100% 100%;
		top: calc(50% - 250px);
		text-align: center;
		padding: 16px 16px 10px;
		z-index: 9;
		color: #EFEFEF;
	}
	
	#user-guide h3 {
		margin-top: 0;
	}
	
	#user-guide ol {
		padding: 0 0 0 20px;
		text-align: left;
		font-size: 16px;
	}
	
	#user-guide>div {
		background-color: rgba(0, 0, 0, 0.7);
		padding: 10px;
	}
	
	#user-guide button {
		background: none;
		border: 1px solid #DFDFDF;
		outline: none;
	}
	
	#question_info {
		position: absolute;
		right: 0;
		top: 50px;
		color: white;
		list-style: none;
		font-size: 22px;
		padding: 0;
		border-top-left-radius: 20px;
		border-bottom-left-radius: 20px;
		z-index: 99;
	}
	
	#question_info li {
		padding-left: 10px;
		height: 50px;
		line-height: 50px;
		transition-duration: 0.5s;
		padding-right: 10px;
		background-color: rgba(0, 0, 0, 0.4);
	}
	
	#question_info li:hover {
		background-color: rgba(0, 0, 0, 0.8);
		color: gold;
		transform: translate(-15px, -3px);
		box-shadow: 10px 3px 10px grey;
	}
	
	#difficulty-choose {
		padding-top: 200px;
		text-align: center;
	}
	
	#difficulty-choose select {
		width: 300px;
		height: 60px;
		font-size: 30px;
		background: none;
		border: none;
		background-color: rgba(0, 0, 0, 0.3);
		color: white;
	}
	
	#difficulty-choose option {
		background: none;
	}
	
	.diff-btn {
		padding-top: 30px;
	}
	
	.diff-btn button {
		color: wheat;
		width: 240px;
		height: 50px;
		font-size: 20px;
		transition-duration: 0.2s;
		display: block;
		margin: 10px auto;
		background: none;
		border: none;
		background-image: url(../assets/img/menu-btn.png);
		background-size: 100% 100%;
		outline: none;
	}
	
	.diff-btn button:hover {
		transform: scale(1.1, 1.1);
	}
	
	kbd {
		background-color: #DFDFDF;
		color: black;
	}
</style>


