<template>
	<div class="chat-log">
		
		<div v-if="chatData.log.userId==userId" style="text-align: right;">
			<div class="realname">{{chatData.log.realname}}</div>
			<span class="log-time">{{(chatData.log.Date.split('T')[0]).split('-')[1]}}-{{(chatData.log.Date.split('T')[0]).split('-')[2]}} {{(this.chatData.log.Date.split('T')[1]).split('.')[0]}}</span>
			<span class="chat-content" :style="{color:'#'+chatData.log.fontColor,fontSize:chatData.log.fontSize,fontFamily:chatData.log.fontFamily}">{{chatData.log.content}}</span>
			<div class="chat-log-avatar-cover ">
				<img v-if="chatData.avatar" @load="imgFix" :src="'/static/avatar/'+chatData.avatar.avatar_src" />
				<img v-if="!chatData.avatar" style="width: 40px;" src="/static/img/default.jpg" />
			</div>

		</div>
		<div v-if="chatData.log.userId!=userId" style="text-align: left;">
			<div class="realname">{{chatData.log.realname}}</div>
			<div class="chat-log-avatar-cover ">
				<img v-if="chatData.avatar" @load="imgFix" :src="'/static/avatar/'+chatData.avatar.avatar_src" />
				<img v-if="!chatData.avatar" style="width: 40px;" src="/static/img/default.jpg" />
			</div>
			<span class="chat-content" :style="{color:'#'+chatData.log.fontColor,fontSize:chatData.log.fontSize,fontFamily:chatData.log.fontFamily}">{{chatData.log.content}}</span>
			<span class="log-time">{{(chatData.log.Date.split('T')[0]).split('-')[1]}}-{{(chatData.log.Date.split('T')[0]).split('-')[2]}} {{(this.chatData.log.Date.split('T')[1]).split('.')[0]}}</span>
		</div>
	</div>
</template>

<script>
	module.exports = {
		props: ['chatData', 'userId'],
		methods: {
			imgFix: function(event) {
				let ev = event || window.event
				let imgWidth = ev.target.width;
				let avatar_data = JSON.parse(this.chatData.avatar.avatar_data);
				//
				$(ev.target).css('width', (imgWidth / avatar_data.width) * 40 + 'px');
				$(ev.target).css('margin-left', -(avatar_data.x / avatar_data.width) * 40 + 'px');
				$(ev.target).css('margin-top', -(avatar_data.y / avatar_data.width) * 40 + 'px');
			}

		},
	}
</script>

<style scoped>
	.chat-log {
		margin: 50px 0;
		position: relative;
	}
	
	.chat-content {
		display: inline-block;
		background-color: #DFDFDF;
		max-width: 50%;
		border-radius: 5px;
		padding: 5px;
		vertical-align: top;
		text-shadow: 1px 1px 2px black;
		min-height: 40px;
	}
	
	.chat-log-avatar-cover {
		width: 40px;
		height: 40px;
		overflow: hidden;
		border-radius: 50%;
		display: inline-block;
		margin-top: 0;
	}
	.log-time{
		color: #DFDFDF;
		vertical-align: top;
		font-size: 8px;
	}
	.realname{
		color: white;
	}
</style>


