import Vue from 'vue'
import Router from 'vue-router'
import Menu from '@/views/menu'
import Rank from '@/views/rank'
import Exam from '@/views/exam'
import FastMode from '@/views/fast_mode'
import FastModeRank from '@/views/fast_mode_rank'
import Gobang from '@/views/gobang'
import GobangLobby from '@/views/gobang-lobby'
import GobangGame from '@/views/game'
import GobangRank from '@/views/gobangRank'
import Chat from '@/views/chat'
import Admin from '@/views/admin'
import Exchange from '@/views/exchange'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect:'/menu'
    },
     {
      path: '/menu',
      name:'Menu',
      component:Menu
    },
     {
      path: '/fast_mode/rank',
      name:'FastModeRank',
      component:FastModeRank
    },
     {
      path: '/exam/rank',
      name:'Rank',
      component:Rank
    },{
      path: '/exam/',
      name:'Exam',
      component:Exam
    },
     {
      path: '/fast_mode',
      name:'FastMode',
      component:FastMode
    },
     {
      path: '/gobang',
      name:'Gobang',
      component:Gobang
    },
    {
      path: '/gobang/lobby',
      name:'GobangLobby',
      component:GobangLobby
    },
    {
      path: '/gobang/game',
      name:'GobangGame',
      component:GobangGame
    },{
      path: '/gobang/rank',
      name:'GobangRank',
      component:GobangRank
    },
     {
      path: '/chat',
      name:'Chat',
      component:Chat
    },
     {
      path: '/admin',
      name:'Admin',
      component:Admin
    },
     {
      path: '/exchange',
      name:'Exchange',
      component:Exchange
    }
  ]
})



