<template>
	<div id="menu">
		<a v-if="menuIndex[0]" class="animated fadeInLeft" @click="menuFun" href="#/gobang"></a>
		<a v-if="menuIndex[1]" class="animated fadeInLeft" @click="menuFun" href="#/exam"></a>
		<a v-if="menuIndex[2]" class="animated fadeInLeft" @click="menuFun" href="#/fast_mode">2</a>
		<a v-if="menuIndex[3]" class="animated fadeInLeft" @click="menuFun" href="#/chat"></a>
	</div>
</template>

<script>
	module.exports = {
		data: function() {
			return {
				menuIndex: []
			}
		},
		methods:{
			menuFun:function(event){
				let ev = event || window.event;
				ev.preventDefault();
				$('#menu a:eq(3)').addClass('fadeOutLeft');
				setTimeout(function(){$('#menu a:eq(2)').addClass('fadeOutLeft');},100)
				setTimeout(function(){$('#menu a:eq(1)').addClass('fadeOutLeft');},200)
				setTimeout(function(){$('#menu a:eq(0)').addClass('fadeOutLeft');},300)
				setTimeout(function(){window.location.href=$(ev.target).attr('href')},800)
			}
		},
		mounted: function() {
			let vue = this;
			$('#app').css({
				'background': 'none',
				'background-image': 'url(static/img/battle.jpg)',
				'background-size': '140% 110%',
				'background-position': '35% 35%'
			})
			vue.menuIndex.push(1)
			
			setTimeout(function() {
				vue.menuIndex.push(1)
			}, 100)
			setTimeout(function() {
				vue.menuIndex.push(1)
			}, 200)
			setTimeout(function() {
				vue.menuIndex.push(1)
			}, 300)
			setTimeout(function() {
				$('#menu a').removeClass('fadeInLeft');
			}, 800)
		}
	}
</script>

<style scoped>
	#menu {
		text-align: center;
		height: 100%;
		padding-top: 200px;
	}
	
	a {
		color: wheat;
		width: 320px;
		height: 65px;
		font-size: 26px;
		transition-duration: 0.2s;
		line-height: 65px;
		display: block;
		margin: 10px auto;
		background: none;
		border: none;
		background-image: url(../assets/img/menu-btn.png);
		background-size: 100% 100%;
		text-decoration: none;
		font-family: "";
		font-weight: bold;
	}
	
	a:hover {
		transform: scale(1.1, 1.1);
	}
</style>


