<template>
	<div id="exchange">
		<div class="table-container">
		<table class="table " >
	 			<thead>
					<tr>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="(gift_info,index) in gift_infos">
						<td>{{index+1}}</td>
						<td>{{gift_info.giftName}}</td>
						<td>{{gift_info.price}}</td>
						<td class="giftOperate"><button @click="numberSub(gift_info.giftName)">-</button><span :data-name="gift_info.giftName">0</span><button @click="numberAdd(gift_info.giftName)">+</button></td>
					</tr>
				</tbody>
	 	</table>
	 	</div>
	 	<div class="bottom animated fadeInUp" v-if="bottomFlag">
	 		<span>{{total}}</span>
	 		<button @click="giftSub"></button>
	 	</div>
	</div>
</template>

<script>
	module.exports = {
		data:function(){
			return{
				gift_infos:[],
				bottomFlag:false,
				total:0
			}
		},
		methods:{
			numberAdd:function(giftName){
				this.bottomFlag=true;
				let cur_num = parseInt($('span[data-name='+ giftName +']').text());
				$('span[data-name='+ giftName +']').text(cur_num+1);
				let total = 0;
					$('tr').each(function(index,item){
						total += $(this).children('td:eq(2)').text()*$(this).children('td:eq(3)').children('span').text();
					})
				this.total = total;
			},
			numberSub:function(giftName){
				let cur_num = parseInt($('span[data-name='+ giftName +']').text());
				if(cur_num>0){
					$('span[data-name='+ giftName +']').text(cur_num-1);
				}
				let total = 0;
					$('tr').each(function(){
						total += $(this).children('td:eq(2)').text()*$(this).children('td:eq(3)').children('span').text();
					})
				this.total = total;
			},
			giftSub:function(){
				let vue = this;
				var data = {
					totalPrice:vue.total,
					content:[]
				};
				$('span[data-name]').each(function(){
					if($(this).text()!='0'){
						data.content.push($(this).attr('data-name')+'x'+$(this).text())
					}
				})
				$.ajax({
				type: "POST",
				url: "http://localhost:3000/gift_order",
				xhrFields: {
					withCredentials: true
				},
				data:{data:window.JSON.stringify(data)},
				crossDomain: true,
				success: function(res) {
					console.log(res)
					if(res.msg=='succ'){
						alert('')
					}else if(res.msg==''){
						alert('')
					}else{
						alert('')
					}
				}
					
				
			})
			}
		},
		mounted:function(){
			let vue = this;
			$('#app').css({
				'background': 'none',
				'background-image': 'url(static/img/battle.jpg)',
				'background-size': '140% 110%',
				'background-position': '40% 50%'
			})
			$.get('http://localhost:3000/gift_query',function(res){
				vue.gift_infos=res;
			})
		}
	}
</script>

<style scoped>
	#exchange{
		padding-top: 50px;
		background-color: rgba(0,0,0,0.5);
		height: 100%;
		width: 100%;
		color: white;
		font-size: 26px;
		position: relative;
		overflow: hidden;
	}
	table{
		text-align: center;
	}
	.giftOperate{
		padding: 0;
	}
	.giftOperate button{
		height: 55px;
		width: 55px;
		outline: none;
		border: none;
		background: none;
		background-image: url(../assets/img/gift-btn.png);
		background-size: 100% 100%;
		font-size: 40px;
		color: #121212;
	}
	.giftOperate span{
		display: inline-block;
		width: 120px;
	}
	th{
		text-align: center;
	}
	.table-container{
		width: 100%;
		height: calc(100% - 80px);
		overflow: auto;
	}
	.bottom{
		position: absolute;
		bottom: 0;
		width: 100%;
		height: 80px;
		background-color: rgba(255,255,255,0.8);
		line-height: 80px;
		padding-left: 40px;
		color: grey;
	}
	.bottom button{
		height: 80px;
		position: absolute;
		right: 0;
		width: 200px;
		font-size: 40px;
		background-color: darkred;
		color: white;
		outline: none;
		border: none;
		transition-duration: 0.3s;
	}
	.bottom button:hover{
		width: 250px;
		background-color: rgb(199,40,40);
	}
</style>


