<template>
	<div id="gobang">
		<div class="lobby">
			<div class="title"></div>
			<div class="lobby-container">
				<i class="fa fa-chevron-left" onclick="javascript:window.location.href='#/gobang'"> </i>
				<i class="fa fa-refresh" @click="refresh"> </i>
				<div v-if="gameDatas" class="game-container" v-for="gameData in gameDatas" @click="startGame(gameData.hoster)">
					<span class="p1">1{{gameData.player[0].userId}}</span>
					<img style="width: 120px;" src="../assets/img/3dc1e4804d46b103b726c95df2401055.png" />
					<span class="p2" v-if="gameData.player[1]">2{{gameData.player[1].userId}}</span>
					<span class="p2 p2-wait" v-if="!gameData.player[1]">2</span>
				</div>
				<div class="game-container addGame" @click="hostGame">
					<div style="font-size: 80px;"><i class="fa fa-plus"></i></div>
					
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	module.exports = {
		data: function() {
			return {
				gameDatas: []
			}
		},
		methods: {
			startGame: function(data) {
				console.log(data)
				$.ajax({
					type: "POST",
					url: "http://localhost:3000/joinGame",
					xhrFields: {
						withCredentials: true
					},
					data: {
						hoster: data
					},
					crossDomain: true,
					success: function(res) {
						console.log(res)
						if(res.msg == 'succ') {
							window.location.href = '#/gobang/game'
						}
					}
				})
			},
			hostGame: function() {
				$.ajax({
					type: "GET",
					url: "http://localhost:3000/hostGame",
					xhrFields: {
						withCredentials: true
					},
					crossDomain: true,
					success: function(res) {
						console.log(res)
						window.location.href = '#/gobang/game'
					}
				})
			},
			refresh: function() {
				let vue = this;
				$.ajax({
					type: "GET",
					url: "http://localhost:3000/lobbyCheck",
					xhrFields: {
						withCredentials: true
					},
					crossDomain: true,
					success: function(res) {
						console.log(res)
						vue.gameDatas = res.data;
						if(res.action == 'redirect') {
							window.location.href = '#/gobang/game'
						}
					}
				})
			}
		},
		mounted: function() {
			let vue = this
			$('#app').css({
				'background': 'none',
				'background-image': 'url(static/img/battle.jpg)',
				'background-size': '140% 110%',
				'background-position': '35% 35%'
			})
			$.ajax({
				type: "GET",
				url: "http://localhost:3000/isLogin",
				xhrFields: {
					withCredentials: true
				},
				crossDomain: true,
				success: function(res) {
					if(!res.data) {
						alert('');
						window.location.href = '#/';
					}
				}
			})
			$.ajax({
				type: "GET",
				url: "http://localhost:3000/lobbyCheck",
				xhrFields: {
					withCredentials: true
				},
				crossDomain: true,
				success: function(res) {
					console.log(res)
					vue.gameDatas = res.data;
					if(res.action == 'redirect') {
						window.location.href = '#/gobang/game'
					}
				}
			})
		}

	}
</script>

<style scoped>
	#gobang {
		height: 100%;
		width: 100%;
		overflow: hidden;
	}
	
	.lobby {
		background-color: rgba(0, 0, 0, 0.5);
		margin-left: 10%;
		margin-top: 80px;
		width: 80%;
		height: 80%;
		border: 10px double #DFDFDF;
		position: relative;
	}
	
	.title {
		text-align: center;
		font-size: 60px;
		color: white;
	}
	
	.lobby-container {
		width: 100%;
		padding: 20px;
		height: calc(100% - 85px);
		overflow: auto;
	}
	
	.game-container {
		width: 30%;
		margin: 1.5%;
		height: 200px;
		color: white;
		cursor: pointer;
		display: inline-block;
		vertical-align: top;
		border: 1px solid #DFDFDF;
		position: relative;
		transition-duration: 0.3s;
		border-radius: 10px;
	}
	
	.addGame {
		border: 5px dashed white;
		font-size: 40px;
		text-align: center;
	}
	
	.addGame:hover {
		border-color: yellow;
		color: yellow;
	}
	
	.game-container>span {
		font-size: 20px;
		font-family: "";
		font-weight: bold;
		color: #EFEFEF;
		text-shadow: 1px 1px 1px black;
	}
	
	.game-container img {
		position: absolute;
		left: calc(50% - 60px);
		top: calc(50px - 20px);
	}
	
	.p1 {
		position: absolute;
		left: 10px;
		top: 5px;
	}
	
	.p2 {
		position: absolute;
		bottom: 5px;
		right: 10px;
	}
	
	.p2-wait {
		transition-duration: 0.2s;
	}
	
	.game-container:hover {
		transform: scale(1.05, 1.05);
	}
	
	.game-container:hover>.p2-wait {
		color: orangered;
		transform: scale(1.2, 1.2);
	}
	
	.fa-refresh {
		position: absolute;
		top: 20px;
		right: 20px;
		font-size: 30px;
		color: white;
		cursor: pointer;
		transition-duration: 0.2s;
	}
	
	.fa-chevron-left {
		position: absolute;
		top: 20px;
		left: 20px;
		font-size: 30px;
		color: white;
		cursor: pointer;
		transition-duration: 0.2s;
	}
	
	.fa:hover {
		transform: scale(1.1, 1.1);
	}
</style>


