<template>
	<div id="profile-container">
	<div id="profile" class="animated fadeIn">
			<div class="reg-container">
				<div class="window_close" @click="panelClose"><span class="glyphicon glyphicon-remove" aria-hidden="true"></span></div>
				<form id="register">
					<div class="form-group">
						<label for="userid"></label><input disabled="disabled" v-model="userId" class="form-control" type="text" id="userid" name="userid" placeholder="" /></div>
					<div class="form-group">
						<label for="oldPassword"></label><input class="form-control" type="password" id="oldPassword" name="oldPassword" placeholder="" /></div>
					<div class="form-group">
						<label for="password"></label><input class="form-control" type="password" id="password" name="password" placeholder="" /></div>
					<div class="form-group">
						<label for="repassword"></label><input class="form-control" type="password" id="repassword" name="repassword" placeholder="" /></div>
					<div class="form-group">
						<label for="realname"></label><input disabled="disabled" v-model="realname" class="form-control" type="text" id="realname" name="realname" placeholder="" /></div>
					<div class="form-group">
						<label for="sex"></label>
						<label class="radio-inline"><input type="radio" name="sex" id="sex_Boy" value="boy" checked/></label>
						<label class="radio-inline"><input type="radio" name="sex" id="sex_Girl" value="girl" /></label>
					</div>
					<div class="form-group">
						<label for="tel">()</label><input v-model="tel" class="form-control" type="text" id="tel" name="tel" placeholder="" /></div>
					<div style="text-align: center;" class="form-btn"><button type="submit" style="position: absolute; left: 80px; bottom: 24px;"></button>
						<button type="button" class="" style="position: absolute; right: 80px; bottom: 24px;"></button></div>
				</form>
			</div>
		</div>
	</div>
</template>

<script>
	module.exports = {
		data:function(){
			return {
				userId:'',
				realname:'',
				tel:''
			}
		},
		props: ['childObj'],
		methods: {
			panelClose: function() {
				this.childObj.isActive = false
			}
		},
		mounted:function(){
			let vue = this;
			$.ajax({
				type: "GET",
				url: "http://localhost:3000/getProfile",
				xhrFields: {
					withCredentials: true
				},
				crossDomain: true,
				success: function(res) {
					vue.userId = res.doc._id;
					vue.realname = res.doc.realname;
					vue.tel = res.doc.tel;
				}
					
				
			})
			$('#register').validate({
				rules: {
					userid: {
						required: true,
						digits: true,
						rangelength: [12, 12]
					},
					oldpassword: {
						required: true,
						rangelength: [4, 20],
					},
					password: {
						required: true,
						rangelength: [4, 20],
					},
					repassword: {
						required: true,
						equalTo: '#password'
					},
					realname: {
						required: true,
						minlength: 2,
						maxlength: 6
					},
					tel: {
						digits: true,
						isphoneNum:true
					}
				},
				messages: {
					userid: {
						digits: '',
						rangelength: '12',
						required: ''
					},
					oldPassword: {
						rangelength: '420',
						required: ''
					},
					password: {
						rangelength: '420',
						required: ''
					},
					repassword: {
						equalTo: '',
						required: ''
					},
					realname: {
						minlength: '',
						maxlength: '',
						required: ''
					},
					tel: {
						digits: '',
						rangelength: '11'
					}
				},
				submitHandler: function(form) {
					var formData = {
						userid: $("#userid").val(),
						oldpwd:$("#oldPassword").val(),
						pwd: $("#repassword").val(),
						realname: $("#realname").val(),
						sex: $("input[name='sex']:checked").val(),
						tel: $("#tel").val()
					}
					$.post('http://localhost:3000/profileChange', formData, function(res) {
						if(res.msg == 'success') {
							alert('');
							vue.childObj.isActive = false
						} else {
							console.log(res)
						}
					})
				}

			})
			jQuery.validator.addMethod("isphoneNum", function(value, element) {
				var length = value.length;
				var mobile = /^1[3|5|7|8]{1}[0-9]{9}$/;
				return this.optional(element) || (length == 11 && mobile.test(value));
			}, "");
		}
	}
</script>

<style scoped>
	#profile-container {
		position: absolute;
		background-color: rgba(0, 0, 0, 0.5);
		width: 100%;
		height: 100%;
		z-index: 9;
		left: 0;
		top: -10px;
		overflow: hidden;
	}
	#profile {
		color: #EFEFEF;
		position: relative;
		top: 160px;
		width: 400px;
		margin: 0 auto;
		background: none;
		padding: 18px 16.5px 13px;
		background-image: url(../assets/img/rule_border.png);
		background-size: 100% 100%;
	}
	
	#profile .reg-container {
		height: 100%;
		width: 100%;
		background-color: rgba(0, 0, 0, 0.7);
		padding: 30px;
		padding-bottom: 50px;
	}
	
	.window_close {
		position: absolute;
		top: 15px;
		right: 15px;
		background: none;
		cursor: pointer;
	}
	
	button {
		background: none;
		background-image: url(../assets/img/diablo-btn.png);
		border: none;
		background-size: 100% 100%;
		width: 105px;
		height: 35px;
		color: #efefef;
		font-weight: bold;
		outline: none;
		transition-duration: 0.2s;
	}
	
	button:hover {
		transform: translateY(-5px);
	}
</style>


