<template>
	<div id="login_register">
		<div id="loginPage" class="animated fadeIn">
			<div class="login-container">
				<div class="window_close" @click="panelClose"><span class="glyphicon glyphicon-remove" aria-hidden="true"></span></div>
				<form id="login">
					<div class="form-group">
						<label for="userid"></label><input class="form-control" type="text" name="userid" id="userid-login" placeholder="" /></div>
					<div class="form-group">
						<label for="password"></label><input class="form-control" type="password" name="password" id="password-login" placeholder="" /></div>
					<div style="text-align: center;" class="form-btn"><button type="submit" class="" style="position: absolute; left: 40px; bottom: 24px;"></button>
						<button type="button" class="" style="position: absolute; right: 40px; bottom: 24px;"></button></div>
				</form>
			</div>
		</div>
		<div id="regPage" class="animated">
			<div class="reg-container">
				<div class="window_close" @click="panelClose"><span class="glyphicon glyphicon-remove" aria-hidden="true"></span></div>
				<form id="register">
					<div class="form-group">
						<label for="userid"></label><input class="form-control" type="text" id="userid" name="userid" placeholder="" /></div>
					<div class="form-group">
						<label for="password"></label><input class="form-control" type="password" id="password" name="password" placeholder="," /></div>
					<div class="form-group">
						<label for="repassword"></label><input class="form-control" type="password" id="repassword" name="repassword" placeholder="" /></div>
					<div class="form-group">
						<label for="realname"></label><input class="form-control" type="text" id="realname" name="realname" placeholder="" /></div>
					<div class="form-group">
						<label for="sex"></label>
						<label class="radio-inline"><input type="radio" name="sex" id="sex_Boy" value="boy" checked/></label>
						<label class="radio-inline"><input type="radio" name="sex" id="sex_Girl" value="girl" /></label>
					</div>
					<div class="form-group">
						<label for="tel">()</label><input class="form-control" type="text" id="tel" name="tel" placeholder="" /></div>
					<div style="text-align: center;" class="form-btn"><button type="submit" style="position: absolute; left: 80px; bottom: 24px;"></button>
						<button type="button" class="" style="position: absolute; right: 80px; bottom: 24px;"></button></div>
				</form>
			</div>
		</div>
	</div>
</template>

<script>
	module.exports = {
		props: ['childObj'],
		methods: {
			panelClose: function() {
				this.childObj.isActive = false
			}
		},
		mounted: function() {
			$('#login .form-btn button[type="button"]').on('click', function() {
				$('#loginPage').removeClass('bounceIn').addClass('fadeOutUp');
				$('#regPage').removeClass('fadeOutDown').addClass('fadeInUp');
				$('#regPage').css('display', 'block');

			})
			$('#register .form-btn button[type="button"]').on('click', function() {
				$('#regPage').removeClass('fadeInDown').addClass('fadeOutDown');
				$('#loginPage').removeClass('fadeOutUp').addClass('fadeInDown');
				$('#regPage').css('display', 'block');
			})
			$('#userid-login').on('change', function() {
				$(this).css("border-color", "rgb(204,204,204)");
				$('#password-longin').css("border-color", "rgb(204,204,204)");
				$('label.error').remove();
			})
			$('#password-login').on('change', function() {
				$(this).css("border-color", "rgb(204,204,204)");
				$('#userid-login').css("border-color", "rgb(204,204,204)");
				$('label.error').remove();
			})
			$('#login').validate({
				rules: {
					userid: {
						required: true,
						digits: true,
						rangelength: [12, 12]
					},
					password: {
						required: true,
						rangelength: [4, 20],
					},
				},
				messages: {
					userid: {
						digits: '',
						rangelength: '12',
						required: ''
					},
					password: {
						rangelength: '420',
						required: ''
					}
				},
				submitHandler: function(form) {
					var formData = {
						userid: $("#userid-login").val(),
						pwd: $("#password-login").val(),
					}
					$.ajax({
						type: "POST",
						url: "http://localhost:3000/login",
						xhrFields: {
							withCredentials: true
						},
						data: formData,
						crossDomain: true,
						success: function(res) {
							switch(res.msg) {
								case 'success':
									window.location.reload();
									break;
								case 'wrong_id':
									$('#userid-login').css("border-color", "#a94442");
									$('#userid-notExist').remove();
									$('#userid-login').after('<label class="error" id="userid-notExist"></label>');
									break;
								case 'wrong_pwd':
									$('#password-login').css("border-color", "#a94442");
									$('#password-wrong').remove();
									$('#password-login').after('<label class="error" id="password-wrong"></label>');
									break;
							}
						},
						error: function() {}
					})
				}

			})
			$('#userid').on('keypress', function() {
				$(this).css("border-color", "rgb(204,204,204)")
				$('#userid-exist').remove();
			})
			$('#register').validate({
				rules: {
					userid: {
						required: true,
						digits: true,
						rangelength: [12, 12]
					},
					password: {
						required: true,
						rangelength: [4, 20],
					},
					repassword: {
						required: true,
						equalTo: '#password'
					},
					realname: {
						required: true,
						minlength: 2,
						maxlength: 6
					},
					tel: {
						digits: true,
						isphoneNum:true
					}
				},
				messages: {
					userid: {
						digits: '',
						rangelength: '12',
						required: ''
					},
					password: {
						rangelength: '420',
						required: ''
					},
					repassword: {
						equalTo: '',
						required: ''
					},
					realname: {
						minlength: '',
						maxlength: '',
						required: ''
					},
					tel: {
						digits: '',
						rangelength: '11'
					}
				},
				submitHandler: function(form) {
					var formData = {
						userid: $("#userid").val(),
						pwd: $("#repassword").val(),
						realname: $("#realname").val(),
						sex: $("input[name='sex']:checked").val(),
						tel: $("#tel").val()
					}
					console.log(formData)
					$.post('http://localhost:3000/register', formData, function(res) {
						if(res.msg == 'success') {
							alert('');
							$('#regPage').removeClass('fadeInUp').addClass('fadeOutDown')
							$('#loginPage').removeClass('fadeOutUp').addClass('fadeInDown')

						} else {
							$('#userid').css("border-color", "#a94442");
							$('#userid-exist').remove();
							$('#userid').after('<label class="error" id="userid-exist"></label>');
						}
					})
				}

			})
			jQuery.validator.addMethod("isphoneNum", function(value, element) {
				var length = value.length;
				var mobile = /^1[3|5|7|8]{1}[0-9]{9}$/;
				return this.optional(element) || (length == 11 && mobile.test(value));
			}, "");
		}
	}
</script>

<style scoped>
	#login_register {
		position: absolute;
		background-color: rgba(0, 0, 0, 0.5);
		width: 100%;
		height: 100%;
		z-index: 9;
		left: 0;
		top: -10px;
		overflow: hidden;
	}
	
	#loginPage {
		color: #EFEFEF;
		position: relative;
		width: 300px;
		height: 300px;
		margin: 20vh auto;
		background: none;
		padding: 11px 12px 6px;
		background-image: url(../assets/img/rule_border.png);
		background-size: 100% 100%;
	}
	
	#loginPage .login-container {
		height: 100%;
		width: 100%;
		background-color: rgba(0, 0, 0, 0.7);
		padding: 30px;
	}
	
	#regPage {
		color: #EFEFEF;
		display: none;
		position: relative;
		top: calc(-300px - 20vh);
		width: 400px;
		margin: 0 auto;
		background: none;
		padding: 18px 16.5px 13px;
		background-image: url(../assets/img/rule_border.png);
		background-size: 100% 100%;
	}
	
	#regPage .reg-container {
		height: 100%;
		width: 100%;
		background-color: rgba(0, 0, 0, 0.7);
		padding: 30px;
		padding-bottom: 50px;
	}
	
	.window_close {
		position: absolute;
		top: 15px;
		right: 15px;
		background: none;
		cursor: pointer;
	}
	
	button {
		background: none;
		background-image: url(../assets/img/diablo-btn.png);
		border: none;
		background-size: 100% 100%;
		width: 105px;
		height: 35px;
		color: #efefef;
		font-weight: bold;
		outline: none;
		transition-duration: 0.2s;
	}
	
	button:hover {
		transform: translateY(-5px);
	}
</style>


