<template>
	<div>
		<div  class="question" :data-index="indexf+1">({{questionObj.index}}.)
			<span>{{questionObj.equ}}</span> =
			<input v-if="!questionObj.finishFlag" class="form-control" @keyup="enterKey" type="text" />
			<input v-if="questionObj.finishFlag" class="form-control" @keyup="enterKeySub" type="text" />
			<button v-if="!questionObj.finishFlag" class="" @click="checkHandle"></button>
			<button v-if="questionObj.finishFlag" class="" :data-diffName="questionObj.diffName" @click="subHandle" id="subBtn"></button>
		</div>
	</div>
</template>

<script>
	module.exports = {
		props: ["questionObj", "index","timer","indexf"],
		data: function() {
			return {
	
			}
		},
		methods: {
			enterKey: function(event) {
				let ev = event || window.event;
				if(ev.keyCode == 13) {
					this.checkHandle(ev)
				}
			},
			enterKeySub: function(event) {
				let ev = event || window.event;
				if(ev.keyCode == 13) {
					this.subHandle(ev)
				}
			},
			checkHandle: function(event) {
				var ev = event || window.event;
				///*
				var ques = ($(ev.target).parent().find('span').text()).replace(//g, '/').replace(//g, '*');
				var ans = $(ev.target).parent().find('input').val();
				$(ev.target).parent().find('input').val(ans);
				$(ev.target).parent().find('input').attr('disabled', '');
				if(ans == '') {
					this.wrongFun(ev);
				} else if(ans.indexOf('\'') != -1 && ans.indexOf('/') != -1) {
					var arr = ans.split('\'');
					var result = parseInt(arr[0]) + eval(arr[1]);
					if((+result).toFixed(2) == (+eval(ques)).toFixed(2)) {
						this.rightFun(ev);
					} else {
						this.wrongFun(ev);
					}

				} else if(ans.indexOf('/') != -1) {
					var arr = ans.split('/');
					console.log(arr[0] + ',' + arr[1]);
					if(arr[0] - arr[1] > 0) {
						this.wrongFun(ev);
					} else if((+eval(ques)).toFixed(2) == (+eval(ans)).toFixed(2)) {
						this.rightFun(ev);
					} else {
						this.wrongFun(ev);
					}
				} else {
					if((+ans).toFixed(2) == (+eval(ques)).toFixed(2)) {
						this.rightFun(ev);
					} else {
						this.wrongFun(ev);
					}
				}
				
				this.index.index++;
				$('div[data-index='+ (this.index.index+1) +'] input').focus();
			},
			//
			rightFun: function(event) {
				var ev = event || window.event;
				$('.question_collec button[data-index="' + $(ev.target).parent().data('index') + '"]').css('color','lightgreen').removeClass('wrong_class').addClass('right_class');
				$('#question_info .right_num span').text($('.question_collec button[class="right_class"]').length);
				$('#question_info .wrong_num span').text($('.question_collec button[class="wrong_class"]').length);
				$('#question_info .right_num').addClass('rubberBand-right');
				setTimeout(function() {
					$('#question_info .right_num').removeClass('rubberBand-right');
				}, 400)
			},
			//
			wrongFun: function(event) {
				var ev = event || window.event;
				$('.question_collec button[data-index="' + $(ev.target).parent().data('index') + '"]').css('color','indianred').removeClass('right_class').addClass('wrong_class');
				$('#question_info .right_num span').text($('.question_collec button[class="right_class"]').length);
				$('#question_info .wrong_num span').text($('.question_collec button[class="wrong_class"]').length);
				$('#question_info .wrong_num').addClass('rubberBand-wrong');
				setTimeout(function() {
					$('#question_info .wrong_num').removeClass('rubberBand-wrong');
				}, 400)
			},
			//
			subHandle: function(event) {
				var ev = event || window.event;
				this.checkHandle(ev);
				this.index.index-=1;
				var totalNum = parseInt($('#question_info .wrong_num span').text()) + parseInt($('#question_info .right_num span').text());
				if(totalNum == $('.question_collec button').length) {
					var subData = {
						diffName: document.getElementById('subBtn').getAttribute("data-diffName"),
						time: 0,
						accuracy: 0,
						userId: ($('.user_info_panel div').text()).split(':')[1]
					}
					console.log(subData.diffName)
					this.timer.stopTimer();
					subData.time = (+this.timer.sec) + (+this.timer.min) * 60 + (+this.timer.hou) * 3600
					subData.accuracy = ($('.question_collec button[class="right_class"]').length / totalNum * 100).toFixed(2);
					//
					$.post('http://localhost:3000/finishHandle', subData, function(res) {
						console.log('');
						$('button[data-diffname]').remove();
					})
					$('#div-mid input').removeAttr('disabled');

					alert('' + this.timer.hou + '' + this.timer.min + '' + this.timer.sec + '' + subData.accuracy + '%,');
				} else {
					alert('');
				}
			}
		}
	}
</script>

<style scoped>
	input {
		width: 180px;
		height: 60px;
		font-size: 60px;
		display: inline-block;
	}
	
	.question {
		text-align: center;
		font-size: 60px;
		padding-top: 40vh;
		color: #666666;
	}
	
	.question span {
		padding: 8px 30px;
		background-color: rgba(0, 0, 0, 0.3);
		border-radius: 5px;
		color: white;
	}
	
	.question button {
		display: block;
		background-color: rgba(0,0,0,0.3);
		border-radius: 10px;
		border: none;
		outline: none;
		width: 100px;
		height: 40px;
		margin: 0 auto;
		font-size: 20px;
		color: white;
	}
	
	.question:nth-child(2) {
		/*display: block;*/
	}
</style>


