<template>
	<div id="admin">
		<div v-if="menuIndex[0]" style="left: 0; top: 0; overflow: auto; padding-top: 50px;" class="admin-block animated fadeIn">
			<table class="table table-condensed">
			<thead>
				<tr>
					<th></th>
					<th></th>
					<th></th>
					<th></th>
					<th></th>
					<th></th>
					<th></th>
				</tr>
			</thead>
			<tbody>
				<tr v-for="diff_info in diff_infos">
					<td>{{diff_info._id}}</td>
					<td>{{diff_info.quantity}}</td>
					<td>{{diff_info.numRange}}</td>
					<td><span v-if="diff_info.extraSetting=='fraction'"></span><span  v-if="diff_info.extraSetting=='decimal'"></span><span v-if="diff_info.extraSetting=='none'"></span></td>
					<td>{{diff_info.dComment}}</td>
					<td>{{diff_info.operator}}</td>
					<td><button @click="diffDelete(diff_info._id)"></button></td>
				</tr>
				
				<form action="http://localhost:3000/diffSub" method="post">
					<td>
						<input type="text" name="diffName" placeholder="" required="required"/>
					</td>
					<td>
						<input type="number" name="quantity" placeholder="" required="required"/>
					</td>
					<td>
						<input type="number" name="numRange" placeholder="" required="required"/>
					</td>
					<td>
						<input type="radio" name="extraSetting" value="none"/>
						<input type="radio" name="extraSetting" value="decimal"/>
						<input type="radio" name="extraSetting" value="fraction"/>
					</td>
					<td>
						<input type="text" name="comment" value="" placeholder="" required="required"/>
					</td>
					<td>
						<input type="checkbox" name="operator" value="add"/>+
						<input type="checkbox" name="operator" value="subtract"/>-
						<input type="checkbox" name="operator" value="multiply"/>
						<input type="checkbox" name="operator" value="divide"/>
					</td>
					<td>
						<button type="submit" ></button>
					</td>
				</form>
			</tbody>
		</table>
		</div><!--
	 --><div v-if="menuIndex[1]" style="right: 0; top: 0; overflow: auto; padding-top: 50px;" class="admin-block animated fadeIn">
	 		<table class="table table-condensed" style="min-width: 500px;">
	 			<thead>
					<tr>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="(notify_info,index) in notify_infos">
						<td>{{index+1}}</td>
						<td>{{(notify_info.pubDate).split('T')[0]}} {{((notify_info.pubDate).split('T')[1]).split('.')[0]}}</td>
						<td>{{notify_info.content}}</td>
						<td><button @click="notifyDelete(notify_info.content)"></button></td>
					</tr>
					<form id="form-notify">
						<td></td>
						<td></td>
						<td>
							<input style="width: 200px;" type="text" name="content" value="" placeholder="" required="required"/>
						</td>
						<td>
							<button type="submit" @click="notifyHandle"></button>
						</td>
					</form>
					
				</tbody>
	 		</table>
	 	</div><!--
	 --><div v-if="menuIndex[3]" style="left: 0; bottom: 0; overflow: auto;" class="admin-block animated fadeIn">
	 		<table  class="table table-condensed" style="min-width: 500px;">
	 			<thead>
					<tr>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="(chatMenber_info,index) in chatMenber_infos">
						<td>{{index+1}}</td>
						<td>{{chatMenber_info.userId}}</td>
						<td>{{chatMenber_info.realname}}</td>
						<td><span v-if="chatMenber_info.lastSpeakDate">{{(chatMenber_info.lastSpeakDate).split('T')[0]}} {{((chatMenber_info.lastSpeakDate).split('T')[1]).split('.')[0]}}</span><span v-if="!chatMenber_info.lastSpeakDate"></span></td>
						<td><span v-if="!chatMenber_info.isBanned" style="color: limegreen;"></span><span v-if="chatMenber_info.isBanned" style="color: red;"></span></td>
						<td><button @click="ban(chatMenber_info.userId)"></button> <button @click="unban(chatMenber_info.userId)"></button></td>
					</tr>
				</tbody>
	 		</table>
	 	</div><!--
	 --><div v-if="menuIndex[2]" style="right: 0; bottom: 0; overflow: auto;" class="admin-block animated fadeIn">
	 		<table class="table table-condensed" style="min-width: 500px;">
	 			<thead>
					<tr>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="(gift_info,index) in gift_infos">
						<td>{{index+1}}</td>
						<td>{{gift_info.giftName}}</td>
						<td>{{gift_info.price}}</td>
						<td><button @click="giftDelete(gift_info.giftName)"></button></td>
					</tr>
					<form id="form-gift">
						<td></td>
						<td><input type="text" name="giftName" placeholder="" style="width: 120px;"/></td>
						<td><input type="number" name="price" placeholder="" style="width: 80px;"/></td>
						<td><button type="submit" @click="giftHandle"></button></td>
					</form>
				</tbody>
	 		</table>
	 		<table class="table table-condensed" style="min-width: 700px;">
	 			<thead>
					<tr>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="(order_info,index) in order_infos">
						<td>{{index+1}}</td>
						<td>{{order_info.userId}}</td>
						<td>{{order_info.subDate.split('T')[0]}}</td>
						<td><div v-for="order in order_info.content">{{order}}</div></td>
						<td><span v-if="order_info.state=='wait'" style="color: red;"></span><span v-if="order_info.state=='finished'" style="color: limegreen;"></span></td>
						<td><button @click="orderFinished(order_info.subDate,order_info.userId)"></button></td>
					</tr>
				</tbody>
	 		</table>
	 	</div>
	</div>
</template>

<script>
	module.exports = {
		data:function(){
			return {
				menuIndex:[],
				diff_infos:[],
				notify_infos:[],
				chatMenber_infos:[],
				gift_infos:[],
				order_infos:[]
			}
		},
		methods:{
			diffDelete:function(diffName){
				console.log(diffName);
				let ev = event || window.event;
				$.get('http://localhost:3000/admin_diff_delete',{diffName:diffName},function(res){
					$(ev.target).parent().parent().remove()
				})
			},
			notifyHandle:function(event){
				let vue = this;
				let ev = event || window.event;
				ev.preventDefault();
				let data={content:$('#form-notify input')[0].value}
				$.post('http://localhost:3000/admin_notify_pub',data,function(res){
					$.get('http://localhost:3000/admin_notify_query',function(res){
							vue.notify_infos=res;
						})
				})
			},
			notifyDelete:function(content){
				let ev = event || window.event;
				$.get('http://localhost:3000/admin_notify_delete',{content:content},function(res){
					$(ev.target).parent().parent().remove();
				})
			},
			ban:function(userId){
				let vue = this;
				$.get('http://localhost:3000/admin_chatMenber_operate',{userId:userId,ope:'ban'},function(res){
					$.get('http://localhost:3000/admin_chatMenber_query',function(res){
					vue.chatMenber_infos=res;
					})
				})
			},
			unban:function(userId){
				let vue = this;
				$.get('http://localhost:3000/admin_chatMenber_operate',{userId:userId,ope:'unban'},function(res){
					$.get('http://localhost:3000/admin_chatMenber_query',function(res){
					vue.chatMenber_infos=res;
					})
				})
			},
			giftHandle:function(event){
				let vue = this;
				let ev = event || window.event;
				ev.preventDefault();
				let data={
					giftName:$('#form-gift input[name="giftName"]')[0].value,
					price:$('#form-gift input[name="price"]')[0].value
				}
				$.post('http://localhost:3000/admin_gift_pub',data,function(res){
					$.get('http://localhost:3000/gift_query',function(res){
						vue.gift_infos=res;
					})
				})
			},
			giftDelete:function(giftName){
				let ev = event || window.event;
				$.get('http://localhost:3000/admin_gift_delete',{giftName:giftName},function(res){
					$(ev.target).parent().parent().remove();
				})
			},
			orderFinished:function(subDate,userId){
				$.post('http://localhost:3000/admin_order_finished',{subDate:subDate,userId:userId},function(res){
					$.get('http://localhost:3000/order_query',function(res){
						vue.order_infos=res;
					})
				})
			}
		}
		,
		mounted:function(){
			let vue = this;
			$('#app').css({
				'background': 'none',
				'background-image': 'url(static/img/battle.jpg)',
				'background-size': '140% 110%',
				'background-position': '40% 50%'
			})
			$.ajax({
				type: "GET",
				url: "http://localhost:3000/isLogin",
				xhrFields: {
					withCredentials: true
				},
				crossDomain: true,
				success: function(res) {
					if(res.msg=='noUser' || res.data.userId !='200000000000') {
						alert('');
						window.location.href="#/"
					}
				}
					
				
			})
			vue.menuIndex.push(1)
			setTimeout(function() {
				vue.menuIndex.push(1)
			}, 200)
			setTimeout(function() {
				vue.menuIndex.push(1)
			}, 400)
			setTimeout(function() {
				vue.menuIndex.push(1)
			}, 600)
			
			$.get('http://localhost:3000/admin_diff_query',function(res){
				vue.diff_infos=res;
			})
			$.get('http://localhost:3000/admin_notify_query',function(res){
				vue.notify_infos=res;
			})
			$.get('http://localhost:3000/admin_chatMenber_query',function(res){
				vue.chatMenber_infos=res;
			})
			$.get('http://localhost:3000/gift_query',function(res){
				vue.gift_infos=res;
			})
			$.get('http://localhost:3000/order_query',function(res){
				vue.order_infos=res;
			})
		}
	}
</script>

<style scoped>
	#admin{
		height: 100%;
		width: 100%;
		position: relative;
	}
	.admin-block{
		position: absolute;
		height: 50%;
		width: 50%;
		background-color: rgba(0,0,0,0.8);
		border: 1px solid #DFDFDF;
	}
	table{
		min-width: 750px;
		text-align: center;
	}
	table th{
		font-size: 18px;
		color: white;
		text-align: center;
	}
	table td{
		font-size: 14px;
	}
	button{
		background: none;
		outline: none;
		border: 1px solid white;
	}
	table tbody tr{
		color: white;
	}
	table tbody tr:hover {
		color: gold;
	}
	form{
		display: table-row;
		color: white;
	}
	input[type='text'],input[type='number']{
		width: 80px;
		color: black;
	}
	
</style>


