<template>
	<div id="app">
		<profile v-if="obj2.isActive" v-bind:childObj="obj2"></profile>
		<login-register v-if="obj.isActive" v-bind:childObj="obj"></login-register>
		<section>
			<div class="main-div animated fadeInLeft" id="div-mid">
				<div class="notify">
					<div class="notify-content">
						<span v-for="(notify,index) in notifys" :style="{left:notify.length*10*((index+1)*2)+'px'}">{{notify}}</span>
					</div>
				</div>
				<router-view/>
			</div>
			<div class="main-div animated fadeInRight" id="div-right">
				<div id="user_info">
					<div class="user_avatar" id="crop-avatar">
						<div class="avatar-view">
							<img src="./assets/img/default.jpg" class="avatar-img" alt="Logo">
						</div>
						<div class="user_avatar_cover"></div>
					</div>
					<span></span>
					<div class="user_info_panel">
						<div>Lv.126</div>
						<ul>
							<li><button type="button" @click="obj2.isActive=true"></button></li>
							<li><button @click="logOut" type="button" class="logout"> </button></li>
							<li><button onclick="javascript:window.location.href='#/exchange'" type="button" ></button></li>
							<li><button v-if="userId=='200000000000'" onclick="javascript:window.location.href='#/admin'" type="button"></button></li>
						</ul>
					</div>
					<button id="btn-login" @click="loginFunc"> </button>
				</div>
				<div class="question_collec"></div>
			</div>
		</section>

		<footer>
			<hr /> (c) Copyright 2017 esby. All Rights Reserved.
		</footer>
		<div class="modal fade" id="avatar-modal" aria-hidden="true" aria-labelledby="avatar-modal-label" role="dialog" tabindex="-1">
			<div class="modal-dialog modal-lg">
				<div class="modal-content">
					<form class="avatar-form" action="http://localhost:3000/avatar_sub" enctype="multipart/form-data" method="post">
						<div class="modal-header">
							<button class="close" data-dismiss="modal" type="button">&times;</button>
							<h4 class="modal-title" id="avatar-modal-label"></h4>
						</div>
						<div class="modal-body">
							<div class="avatar-body">
								<div class="avatar-upload">
									<input class="avatar-src" name="avatar_src" type="hidden">
									<input class="avatar-data" name="avatar_data" type="hidden">
									<label for="avatarInput"></label>
									<input class="avatar-input" id="avatarInput" name="avatar_file" type="file" accept="image/jpeg,image/jpg,image/png"></div>
								<div class="row">
									<div class="col-md-9">
										<div class="avatar-wrapper"></div>
									</div>
									<div class="col-md-3">
										<div class="avatar-preview preview-lg"></div>
										<div class="avatar-preview preview-md"></div>
										<div class="avatar-preview preview-sm"></div>
									</div>
								</div>
								<div class="row avatar-btns">
									<div class="col-md-3">
										<button class="btn btn-success btn-block avatar-save" type="submit"><i class="fa fa-save"></i> </button>
									</div>
								</div>
							</div>
						</div>
					</form>
				</div>
			</div>
		</div>

	</div>
</template>

<script>
	module.exports = {
		data: function() {
			return {
				obj: {
					isActive: false
				},
				obj2:{
					isActive: false
				},
				notifys:[],
				notifyCss:0,
				userId:''

			}
		},
		components: {
			LoginRegister: require('./components/login_register').default,
			Profile: require('./components/profile').default
		},
		methods: {
			notifyAnimate:function(){
				let left = -(-$('.notify-content span:eq(0)').css('left').split('px')[0]);
				$('.notify-content span').each(function(){
					let left = -(-$(this).css('left').split('px')[0]);
					if(left<-900) left=900;
					left-=0.5;
					$(this).css('left',left+'px');
					
				})
				requestAnimationFrame(this.notifyAnimate);
				
			},
			loginFunc: function() {
				this.obj.isActive = true;
			},
			logOut: function() {
				$.ajax({
					type: "GET",
					url: "http://localhost:3000/logout",
					xhrFields: {
						withCredentials: true
					},
					crossDomain: true,
					success: function(res) {
						window.location.reload();
					},
					error: function() {}
				})
			}
		},
		mounted: function() {
			let vue = this;
			$.ajax({
				type: "GET",
				url: "http://localhost:3000/isLogin",
				xhrFields: {
					withCredentials: true
				},
				crossDomain: true,
				success: function(res) {
					if(res.data) {
						vue.userId = res.data.userId;
						$('#btn-login').css('display', 'none');
						$('#user_info span').css('display', 'block');
						$('#user_info span').text(',' + res.data.userName + '(Lv.' + res.data.userLvl + ')');
						$('#user_info .user_info_panel div').text('ID:' + res.data.userId);
						$('.user_info_panel').css('display', 'block')
						$('.user_avatar').on('mouseover', function() {
							$('.user_avatar_cover').css('display', 'block');
						}).css('cursor', 'pointer')
						$('.user_avatar').on('mouseout', function() {
							$('.user_avatar_cover').css('display', 'none');
						})
						$('.user_avatar').css('pointer-events', 'auto');
						$('.guest-page').css('display', 'none');
						$('.user-page').css('display', 'block');
						if(res.avatar) {
							var avatar_data = JSON.parse(res.avatar.avatar_data);
							$('.user_avatar img').attr('src', 'static/avatar/' + res.avatar.avatar_src);
							$('.user_avatar img').removeClass('avatar-img');
							$('.user_avatar img').on('load', function() {
								//
								var imgWidth = $('.user_avatar img')[0].width;
								//
								$(this).css('width', (imgWidth / avatar_data.width) * 150 + 'px');
								$(this).css('margin-left', -(avatar_data.x / avatar_data.width) * 150 + 'px');
								$(this).css('margin-top', -(avatar_data.y / avatar_data.width) * 150 + 'px');
							})
						}
					}
					
				}
			})
			$.get('http://localhost:3000/admin_notify_query',function(res){
				for(let i = res.length-1;i>=res.length-2;i--){
					let notify = (res[i].pubDate).split('T')[0] + ' ' + ((res[i].pubDate).split('T')[1]).split('.')[0] + '' +res[i].content;
					vue.notifys.push(notify)
				}
				requestAnimationFrame(vue.notifyAnimate);
			})
			
		}
	}
</script>

<style>
	html {
		height: 100%;
		margin: 0;
		padding: 0;
	}
	
	body {
		position: relative;
		width: 100%;
		height: 100%;
		min-height: 768px;
		min-width: 1280px;
		/*overflow: hidden;*/
	}
	
	hr {
		margin: 0;
	}
	
	#app {
		height: 100%;
		overflow: hidden;
		width: 100%;
	}
	
	.notify{
		position: absolute;
		width: 600px;
		height: 100px;
		top: -45px;
		left: calc(50% - 300px);
		background-image: url(assets/img/notice.png);
		background-size: 100% 100%;
		transition-duration: 0.5s;
		z-index: 10;
	}
	.notify:hover{
		transform: translateY(30px);
	}
	.notify-content{
		color: white;
		position: relative;
		overflow: hidden;
		height: 30px;
		width: 400px;
		margin: 60px auto 0;
	}
	.notify-content span{
		position: absolute;
		z-index: 99;
		font-size: 16px;
		display: inline-block;
		height: 18px;
		overflow: hidden;
	}
	section {
		padding-right: 250px;
		height: calc(100% - 60px);
		width: 100%;
		padding-top: 10px;
		padding-left: 10px;
	}
	
	header {
		padding: 10px;
		position: relative;
		border-bottom: 1px solid #dddddd;
		height: 55px;
		box-shadow: 0px 1px 10px #dddddd;
		background-color: rgba(0, 0, 0, 0.3);
	}
	
	#app footer {
		position: absolute;
		max-width: 100%;
		width: 100%;
		bottom: 0px;
		color: #222222;
		text-align: center;
		line-height: 40px;
		background-color: rgba(255, 255, 255, 0.7);
		font-size: 12px;
	}
	
	label.error {
		color: #a94442;
	}
	
	.avatar-img {
		width: 100%;
	}
	
	.user_avatar {
		height: 150px;
		width: 150px;
		background-color: grey;
		margin: 0 auto 20px;
		border-radius: 50%;
		overflow: hidden;
		pointer-events: none;
		position: relative;
	}
	
	.user_avatar_cover {
		position: absolute;
		background-color: rgba(0, 0, 0, 0.3);
		font-size: 16px;
		height: 60px;
		width: 100%;
		bottom: -10px;
		line-height: 30px;
		display: none;
		pointer-events: none;
	}
	
	#btn-login {
		height: 40px;
		width: 170px;
		background: none;
		outline: none;
		border: none;
		color: #DFDFDF;
		font-size: 16px;
		transition-duration: 0.3s;
		background-image: url(assets/img/diablo-btn.png);
		background-size: 100% 100%;
	}
	
	#btn-login:hover {
		transform: scale(1.1, 1.1);
		color: gold;
	}
	
	.user_info_panel button {
		height: 40px;
		width: 170px;
		border: none;
		outline: none;
		background: none;
		background-image: url(assets/img/diablo-btn.png);
		background-size: 100% 100%;
		color: #DFDFDF;
		font-weight: bold;
		font-size: 14px;
		transition-duration: 0.3s;
	}
	
	.user_info_panel button:hover {
		color: gold;
	}
	
	.window_close {
		position: absolute;
		height: 15px;
		width: 15px;
		top: 15px;
		right: 15px;
		background-color: rgba(255, 255, 255, 1);
		cursor: pointer;
	}
	
	#user_info {
		text-align: center;
		padding: 50px 0;
		color: white;
		font-size: 18px;
		position: relative;
	}
	
	.main-div {
		height: 100%;
		float: left;
		background-color: rgba(255, 255, 255, 0.4);
		border-radius: 5px;
	}
	
	#div-right {
		width: 220px;
		margin-left: -220px;
		position: relative;
		right: -240px;
		background-color: rgba(0, 0, 0, 0.3);
		background-image: url(assets/img/border.png);
		background-size: 100% 99.5%;
		background-position-y: 12%;
	}
	
	#div-mid {
		width: 100%;
		position: relative;
	}
	
	.user_info_panel {
		display: none;
		text-align: center;
	}
	
	.user_info_panel ul {
		padding: 10px 0 0 0;
	}
	
	.user_info_panel ul li {
		list-style: none;
		margin-bottom: 10px;
	}
	
	
	.rank-table {
		transition-duration: 0.5s;
		margin: 50px auto 0 auto;
		width: 95% !important;
		background-color: rgba(0, 0, 0, 0.5);
		color: #EEEEEE;
		border-radius: 10px;
		font-family: "";
	}
	
	.rank-table thead {
		font-size: 18px;
	}
	
	.rank-table tbody tr {
		height: 80px;
		font-size: 38px;
		padding: 0;
		line-height: 80px;
		font-weight: bold;
		transition-duration: 0.3s;
		text-align: center;
	}
	
	.rank-table tbody tr:hover {
		color: #555555;
		font-weight: bold;
		transform: scale(1.05, 1.2);
	}
	
	.rank-table tbody tr:nth-of-type(n+4) {
		height: 40px;
		font-size: 16px;
		line-height: 40px;
	}
	
	.rank-table tbody tr:nth-of-type(1) {
		font-size: 40px;
	}
	
	.rank-table tbody tr:nth-of-type(2) {
		font-size: 35px;
	}
	
	.rank-table tbody tr:nth-of-type(3) {
		font-size: 30px;
	}
	
	.table-avatar-cover {
		width: 60px;
		height: 60px;
		overflow: hidden;
		border-radius: 50%;
	}
	.question_collec {
		height: calc(100% - 490px);
		overflow: auto;
		padding: 5px;
	}
	
	.question_collec button {
		width: calc(25% - 6px);
		height: 40px;
		border: none;
		background: none;
		background-image: url(assets/img/button.png);
		background-size: 100% 100%;
		background-repeat: no-repeat;
		margin: 3px;
		transition-duration: 0.3s;
		outline: none;
		font-weight: bold;
		font-size: 18px;
	}
	
	.question_collec button:hover {
		transform: scale(1.2, 1.2);
	}
	#chat .chat-avatar-cover{
		width: 40px;
		height: 40px;
		overflow: hidden;
		border-radius: 50%;
		margin-right: 10px;
		display: inline-block;
	}
	#chat .user-block{
		position: relative;
		zoom: 1;
		padding: 5px;
		transition-duration: 0.2s;
		margin: 10px;
	}
	#chat .user-block>div{
		float: left;
		font-size: 24px;
		line-height: 40px;
		color: white;
	}
	#chat .user-block:after{
		content: '.';
		height: 0;
		display: block;
		clear: both;
		visibility: hidden;
	}
	#chat .last-speak{
		display: block;
		padding-top: 5px;
		color: #aaaaaa !important;
		font-size: 10px !important;
		line-height: 10px !important;
		clear: both;
	}
	#chat .user-block:hover{
		transform: scale(1.1,1.1);
	}
	#chat .online-state-online{
		position: absolute;
		right: 10px;
		display: inline-block;
		height: 20px;
		width: 20px;
		border-radius: 50%;
		background-color: limegreen;
		top: calc(50% - 15px);
	}
	#chat .online-state-offline{
		position: absolute;
		right: 10px;
		display: inline-block;
		height: 20px;
		width: 20px;
		border-radius: 50%;
		background-color: grey;
		top: calc(50% - 15px);
	}
</style>


