<template>
	<div id="gobang-rank">
		<button type="button" class="btn-back" onclick="javascript:window.location.href='#/gobang'"> </button>
		<table class="table table-hover rank-table">
			<thead>
				<tr>
					<th></th>
					<th></th>
					<th></th>
					<th></th>
					<th></th>
					<th></th>
				</tr>
			</thead>
			<tbody>
				
			</tbody>
		</table>
	</div>
</template>

<script>
	module.exports={
		mounted:function(){
			let vue = this;
			$('#app').css({
				'background': 'none',
				'background-image': 'url(static/img/battle.jpg)',
				'background-size': '140% 110%',
				'background-position': '35% 35%'
			})
			$.get('http://localhost:3000/gobangRankQuery', function(res) {
				let imgWidth = 100;
				let arrAvatar = [];
				$.each(res.arrAvatar, function(index, item) {
					if(item) {
						arrAvatar.push(JSON.parse(item.avatar_data))
					} else {
						arrAvatar.push(null)
					}
				});
				for(let i = 0; i < 10; i++) {
					if(res.rank[i]) {
						if(i < 3) {
							for(let j = 0; j < 3; j++) {
								if(res.rank[i] && res.arrAvatar[j] != null && res.arrAvatar[j]._id == res.rank[i].userId) {
									$('table tbody').append('<tr><td>' + (+i + 1) + '.</td><td><div class="table-avatar-cover"><img src=' + "/static/avatar/" + res.arrAvatar[j].avatar_src + ' data-index=' + i + ' /></div></td><td>' + (res.rank[i]).realname + '</td><td>' + (res.rank[i]).userId + '</td><td>' + res.rank[i].winNumb + '</td><td>' + res.rank[i].loseNumb + '</td></tr>');
									$('table img[data-index="' + i + '"]').on('load', function() {
										const imgWidth = $(this)[0].width;
										$(this).css('width', (imgWidth / arrAvatar[j].width) * 60 + 'px');
										$(this).css('margin-left', -(arrAvatar[j].x / arrAvatar[j].width) * 60 + 'px');
										$(this).css('margin-top', -(arrAvatar[j].y / arrAvatar[j].width) * 60 + 'px');
									})
									break;
								} else {
									if(j == 2) {
										$('table tbody').append('<tr><td>' + (+i + 1) + '.</td><td><div class="table-avatar-cover"><img style="width: 60px;" src="/static/img/default.jpg" /></div></td><td>' + (res.rank[i]).realname + '</td><td>' + (res.rank[i]).userId + '</td><td>' + res.rank[i].winNumb + '</td><td>' + res.rank[i].loseNumb + '</td></tr>');
									}
								}
							}
						} else {
							$('table tbody').append('<tr><td>' + (+i + 1) + '.</td><td></td><td>' + (res.rank[i]).realname + '</td><td>' + (res.rank[i]).userId + '</td><td>' + res.rank[i].winNumb + '</td><td>' + res.rank[i].loseNumb + '</td></tr>');
						}

					} else {
						$('table tbody').append('<tr><td>' + (+i + 1) + '.</td><td></td><td></td><td></td><td></td><td></td></tr>')
					}
				}
			})
		}
	}
</script>

<style scoped>
	#gobang-rank {
		position: relative;
		height: 100%;
		width: 100%;
		padding-top: 60px;
	}
	
	.btn-back {
		position: absolute;
		left: 40px;
		top: 60px;
		font-size: 20px;
		background-color: rgba(0, 0, 0, 0.5);
		color: white;
		border: none;
		padding: 5px 20px;
		border-radius: 8px;
		transition-duration: 0.2s;
		font-weight: bold;
		outline: none;
	}
	
	.btn-back:hover {
		background-color: rgba(0, 0, 0, 0.9);
	}
</style>


