<template>
	<div id="gobang">
		<div class="title">
			<div></div>
			<button v-if="menuIndex[0]" @click="toLobby" class="animated fadeInLeft"></button>
			<button v-if="menuIndex[1]" @click="ruleFlag=true" class="animated fadeInLeft"></button>
			<button v-if="menuIndex[2]" @click="rank" class="animated fadeInLeft"></button>
			<button v-if="menuIndex[3]" @click="back" class="animated fadeInLeft"></button>
		</div>
		
		<div v-if="ruleFlag" id="user-guide" class="animated fadeIn">
			<div>
				<h3></h3>
				<ol>
					<li></li>
					<li></li>
					<li>2</li>
					<li></li>
					<li>5</li>
					<li></li>
				</ol>
				<button type="button" @click="ruleFlag=false"></button>
			</div>
		</div>
	</div>
</template>

<script>
	module.exports = {
		data: function() {
			return {
				ruleFlag: false,
				menuIndex: []
			}
		},
		methods: {
			rank: function() {
				this.menuOutFun();
				setTimeout(function() {
					window.location.href = '#/gobang/rank';
				}, 800)
			},
			back: function() {
				this.menuOutFun();
				setTimeout(function() {
					window.location.href = '#/';
				}, 800)
			},
			toLobby: function() {
				this.menuOutFun();
				setTimeout(function() {
					window.location.href = '#/gobang/lobby';
				}, 800)

			},
			menuFun: function() {
				let a = this.menuIndex
				a.push(1)
				setTimeout(function() {
					a.push(1)
				}, 100)
				setTimeout(function() {
					a.push(1)
				}, 200)
				setTimeout(function() {
					a.push(1)
				}, 300)
				setTimeout(function() {
					$('.title button').removeClass('fadeInLeft')
				}, 800)
			},
			menuOutFun:function(){
				$('.title button:eq(3)').addClass('fadeOutLeft');
				setTimeout(function() {
					$('.title button:eq(2)').addClass('fadeOutLeft');
				}, 100)
				setTimeout(function() {
					$('.title button:eq(1)').addClass('fadeOutLeft');
				}, 200)
				setTimeout(function() {
					$('.title button:eq(0)').addClass('fadeOutLeft');
				}, 300)
			}
		},
		mounted: function() {
			$.ajax({
				type: "GET",
				url: "http://localhost:3000/isLogin",
				xhrFields: {
					withCredentials: true
				},
				crossDomain: true,
				success: function(res) {
					if(!res.data) {
					alert('');
					window.location.href='#/';
				}
				}
			})
			$('#app').css({
				'background': 'none',
				'background-image': 'url(static/img/battle.jpg)',
				'background-size': '140% 110%',
				'background-position': '35% 35%'
			})
			this.menuFun();
			
		},

	}
</script>

<style scoped>
	#gobang {
		position: relative;
		text-align: center;
		height: 100%;
		overflow: hidden;
	}
	
	.title {
		width: 600px;
		margin: 0 auto;	
		text-align: center;
		padding-top: 120px;
	}
	
	.title div {
		font-family: "";
		font-size: 80px;
		color: #efefef;
		background-color: rgba(0, 0, 0, 0.5);
		border-radius: 15px;
		margin-bottom: 100px;
	}
	
	.title button {
		display: block;
		background: none;
		background-image: url(../assets/img/menu-btn.png);
		background-size: 100% 100%;
		border: none;
		outline: none;
		width: 240px;
		height: 50px;
		color: wheat;
		font-size: 20px;
		margin: 20px auto;
		transition-duration: 0.2s;
	}
	
	.title button:hover {
		transform: scale(1.1, 1.1);
	}
	
	
	
	#user-guide {
		position: absolute;
		left: calc(50% - 200px);
		width: 400px;
		background-image: url(../assets/img/rule_border.png);
		background-size: 100% 100%;
		top: calc(50% - 250px);
		text-align: center;
		padding: 13px 16px 8px;
		z-index: 9;
		color: #EFEFEF;
	}
	
	#user-guide h3 {
		margin-top: 0;
	}
	
	#user-guide ol {
		padding: 0 0 0 20px;
		text-align: left;
		font-size: 18px;
		line-height: 30px;
	}
	
	#user-guide>div {
		background-color: rgba(0, 0, 0, 0.7);
		padding: 10px;
	}
	
	#user-guide button {
		background: none;
		border: 1px solid #DFDFDF;
		outline: none;
	}
	
</style>


