<template>
	<div id="fast_mode">
		<div v-if="!gameFlag" class="title">
			<div>2</div>
			<button v-if="menuIndex[0]" @click="startGame" class="animated fadeInLeft"></button>
			<button v-if="menuIndex[1]" @click="ruleFlag=true" class="animated fadeInLeft"></button>
			<button v-if="menuIndex[2]" @click="rank" class="animated fadeInLeft"></button>
			<button v-if="menuIndex[3]" @click="back" class="animated fadeInLeft"></button>
		</div>
		<div v-if="gameFlag" class="game">
			<div class="game-progress">
				<div class="game-progress-bar" :style="{width:barWidth2}">

				</div>
			</div>
			<div class="game-question">
				{{equ}} = ?
			</div>
			<div class="game-select" @click="checkResult">
				<button class="selector" v-for="result in results" :data-state="result.state">{{result.num}}</button>
			</div>
		</div>
		<div v-if="ruleFlag" id="user-guide" class="animated fadeIn">
			<div>
				<h3></h3>
				<ol>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
					<li></li>
				</ol>
				<button type="button" @click="ruleFlag=false"></button>
			</div>
		</div>
		<div v-if="countDown" class="countdown animated fadeIn">
			{{countDown}}
		</div>
	</div>
</template>

<script>
	module.exports = {
		data: function() {
			return {
				ruleFlag: false,
				menuIndex: [],
				gameFlag: false,
				barWidth1: 0,
				barWidth2: '100%',
				equ: '',
				results: [],
				rightNumb: 0,
				countDown: 0
			}
		},
		methods: {
			rank: function() {
				this.menuOutFun();
				setTimeout(function() {
					window.location.href = '#/fast_mode/rank';
				}, 800)
			},
			back: function() {
				this.menuOutFun();
				setTimeout(function() {
					window.location.href = '#/';
				}, 800)
			},
			startGame: function() {
				let vue = this
				this.menuOutFun();
				$('.title>div').addClass('animated fadeOutUp');
				setTimeout(function() {
					vue.menuIndex = []; //
					vue.countDown = 5;
					vue.rightNumb = 0;
					let count = setInterval(function() {
						vue.countDown--;
						if(!vue.countDown) {
							clearInterval(count)
							vue.gameFlag = true;
							vue.nextQuestion();
							requestAnimationFrame(vue.barControl);
						}
					}, 1000)
				}, 800)

			},
			nextQuestion: function() {
				this.setQuestion();
				this.barWidth1 = 220;

			},
			barControl: function() {
				this.barWidth2 = this.barWidth1 / 220 * 100 + '%';
				this.barWidth1 -= 1;
				if(this.barWidth1 > 0) requestAnimationFrame(this.barControl);
			},
			setQuestion: function() {
				let operator = ['+', '-'];
				let ope = operator[Math.round(Math.random())];
				let a = 0,
					b = 0,
					fakeRes1 = 0,
					fakeRes2 = 0,
					fakeRes3 = 0;
				let result = [];

				if(this.rightNumb < 6) {
					a = Math.ceil(Math.random() * 5);
					b = Math.ceil(Math.random() * a);
				} else if(this.rightNumb < 12) {
					a = Math.ceil(Math.random() * 10);
					b = Math.ceil(Math.random() * a);
				} else if(this.rightNumb < 18) {
					a = Math.ceil(Math.random() * 15);
					b = Math.ceil(Math.random() * 15);
				} else if(this.rightNumb < 24) {
					a = Math.ceil(Math.random() * 20);
					b = Math.ceil(Math.random() * 20);
				} else {
					a = Math.ceil(Math.random() * 25);
					b = Math.ceil(Math.random() * 25);
				}
				this.equ = a + ' ' + ope + ' ' + b;
				if(ope == '+') {
					result[0] = {
						num: a + b,
						state: 'right'
					};
				} else {
					result[0] = {
						num: a - b,
						state: 'right'
					};
				}
				for(let i = 1; i < 4; i++) {
					if(operator[Math.round(Math.random())] == '+') {
						result[i] = {
							num: result[0].num + i,
							state: 'wrong'
						};
					} else {
						result[i] = {
							num: result[0].num - i,
							state: 'wrong'
						};
					}
				}
				this.results = []
				for(let j = 0; j < 4; j++) {
					if(Math.round(Math.random()) > 0.5) {
						this.results.push(result[j]);
					} else {
						this.results.unshift(result[j]);
					}
				}
			},
			checkResult: function(event) {
				let ev = event || window.event;
				if(ev.target.getAttribute('data-state') == 'right') {
					this.rightNumb += 1;
					this.nextQuestion();
				} else if(ev.target.getAttribute('data-state') == 'wrong') {
					this.endFun();
				}
			},
			endFun: function() {
				let subData={
					rightNumb:this.rightNumb,
					userId: ($('.user_info_panel div').text()).split(':')[1]
				}
				alert('' + this.rightNumb + '');
				$.post('http://localhost:3000/fastModeSub', subData, function(res) {
						console.log('');
						
					})
				this.gameFlag = false;
				this.menuFun();
			},
			menuFun: function() {
				let a = this.menuIndex
				a.push(1)
				setTimeout(function() {
					a.push(1)
				}, 100)
				setTimeout(function() {
					a.push(1)
				}, 200)
				setTimeout(function() {
					a.push(1)
				}, 300)
				setTimeout(function() {
					$('.title button').removeClass('fadeInLeft')
				}, 800)
			},
			menuOutFun:function(){
				$('.title button:eq(3)').addClass('fadeOutLeft');
				setTimeout(function() {
					$('.title button:eq(2)').addClass('fadeOutLeft');
				}, 100)
				setTimeout(function() {
					$('.title button:eq(1)').addClass('fadeOutLeft');
				}, 200)
				setTimeout(function() {
					$('.title button:eq(0)').addClass('fadeOutLeft');
				}, 300)
			}
		},
		watch: {
			barWidth1: function() {
				if(this.barWidth1 / 220 * 100 > 70) {
					$('.game-progress-bar').css('background-color', 'limegreen');
				} else if(this.barWidth1 / 220 * 100 > 35) {
					$('.game-progress-bar').css('background-color', 'gold');
				} else {
					$('.game-progress-bar').css('background-color', 'orangered');
				}
				if(this.barWidth1 == 0 && this.gameFlag == true) {
					this.endFun();
				}
			}
		},
		mounted: function() {
			$.ajax({
				type: "GET",
				url: "http://localhost:3000/isLogin",
				xhrFields: {
					withCredentials: true
				},
				crossDomain: true,
				success: function(res) {
					if(!res.data) {
					alert('');
					window.location.href='#/';
				}
				}
			})
			$('#app').css({
				'background': 'none',
				'background-color': 'brown',
				'background-image': 'url(static/img/digit.png)',
				'background-size': 'auto !important'
			})
			this.menuFun();
			
		},

	}
</script>

<style scoped>
	#fast_mode {
		position: relative;
		text-align: center;
		height: 100%;
		overflow: hidden;
	}
	
	.title {
		width: 1000px;
		margin: 0 auto;
		text-align: center;
		padding-top: 120px;
	}
	
	.title div {
		font-family: "";
		font-size: 80px;
		color: #efefef;
		background-color: rgba(0, 0, 0, 0.5);
		border-radius: 15px;
		margin-bottom: 100px;
	}
	
	.title button {
		display: block;
		background: none;
		background-image: url(../assets/img/menu-btn.png);
		background-size: 100% 100%;
		border: none;
		outline: none;
		width: 240px;
		height: 50px;
		color: wheat;
		font-size: 20px;
		margin: 20px auto;
		transition-duration: 0.2s;
	}
	
	.title button:hover {
		transform: scale(1.1, 1.1);
	}
	
	.game-progress {
		width: 500px;
		height: 100px;
		margin: 150px auto;
		background-color: white;
		border-radius: 20px;
		overflow: hidden;
		margin-bottom: 50px;
	}
	
	.game-progress-bar {
		height: 100%;
		width: 100%;
		background-color: limegreen;
		position: relative;
	}
	
	.game-question {
		display: inline;
		background-color: rgba(0, 0, 0, 0.5);
		color: white;
		font-size: 80px;
		padding: 10px;
		border-radius: 10px;
	}
	
	.game-select button {
		width: 100px;
		height: 100px;
		background: none;
		border: none;
		outline: none;
		background-image: url(../assets/img/button.png);
		background-size: 100% 98%;
		background-repeat: no-repeat;
		font-size: 40px;
		font-family: ;
		padding-bottom: 15px;
		margin: 30px 20px;
		transition-duration: 0.2s;
	}
	
	.game-select button:hover {
		transform: scale(1.2, 1.2);
	}
	
	#user-guide {
		position: absolute;
		left: calc(50% - 200px);
		width: 400px;
		background-image: url(../assets/img/rule_border.png);
		background-size: 100% 100%;
		top: calc(50% - 250px);
		text-align: center;
		padding: 13px 16px 8px;
		z-index: 9;
		color: #EFEFEF;
	}
	
	#user-guide h3 {
		margin-top: 0;
	}
	
	#user-guide ol {
		padding: 0 0 0 20px;
		text-align: left;
		font-size: 18px;
		line-height: 30px;
	}
	
	#user-guide>div {
		background-color: rgba(0, 0, 0, 0.7);
		padding: 10px;
	}
	
	#user-guide button {
		background: none;
		border: 1px solid #DFDFDF;
		outline: none;
	}
	
	.countdown {
		position: absolute;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.7);
		top: 0;
		left: 0;
		color: white;
		font-size: 300px;
		padding-top: 180px;
	}
</style>


