<template>
	<div id="rank">
		<button type="button" class="btn-back" onclick="javascript:window.location.href='#/exam'"> </button>
		<div id="difficulty-choose">
			<select name="difficulty">
				<option value="0"></option>
			</select>

		</div>
	</div>
</template>

<script>
	module.exports = {
		mounted: function() {
			$('#app').css({
				'background-color': 'brown',
				'background-image': 'url(static/img/digit.png)',
				'background-size': 'auto !important'
			})
			$.get('http://localhost:3000/difficulty', function(res) {
				$.each(res, function(index, item) {
					$('#difficulty-choose select').append('<option value="' + item + '">' + item + '</option>');
				})
			})
			$('#difficulty-choose select').on('change', function() {
				if($(this).val() == 0) return;
				$.get('http://localhost:3000/rankQuery', {
					diffName: $(this).val()
				}, function(res) {
					$('table').remove();
					$('#rank').append('<table class="table table-hover rank-table animated fadeIn"><thead><tr><th></th><th></th><th></th><th></th><th></th><th></th></tr></thead><tbody></tbody></table>')
					var arrAvatar = [];
					$.each(res.arrAvatar, function(index, item) {
						if(item) {
							arrAvatar.push(JSON.parse(item.avatar_data))
						} else {
							arrAvatar.push(null)
						}
					});
					for(let i = 0; i < 10; i++) {
						if(res.rank[i]) {

							var hour = (res.rank[i].time - (res.rank[i].time % 3600)) / 3600;
							var min = ((res.rank[i].time - hour * 3600) - (res.rank[i].time - hour * 3600) % 60) / 60;
							var sec = res.rank[i].time - hour * 3600 - min * 60;
							sec.toString().length < 2 ? sec = '0' + sec : sec = sec;
							min.toString().length < 2 ? min = '0' + min : min = min;
							if(i < 3) {
								for(let j = 0; j < 3; j++) {
									if(res.rank[i] && res.arrAvatar[j] != null && res.arrAvatar[j]._id == res.rank[i].userId) {
										$('table tbody').append('<tr><td>' + (+i + 1) + '.</td><td><div class="table-avatar-cover"><img src=' + "/static/avatar/" + res.arrAvatar[j].avatar_src + ' data-index=' + i + ' /></div></td><td>' + (res.rank[i]).realname + '</td><td>' + (res.rank[i]).userId + '</td><td>' + res.rank[i].accuracy + '%</td><td>' + hour + '' + min + '' + sec + '</td></tr>');
										$('table img[data-index="' + i + '"]').on('load', function() {
											const imgWidth = $(this)[0].width;
											console.log(imgWidth)
											$(this).css('width', (imgWidth / arrAvatar[j].width) * 60 + 'px');
											$(this).css('margin-left', -(arrAvatar[j].x / arrAvatar[j].width) * 60 + 'px');
											$(this).css('margin-top', -(arrAvatar[j].y / arrAvatar[j].width) * 60 + 'px');
										})
										break;
									} else {
										if(j == 2) {
											$('table tbody').append('<tr><td>' + (+i + 1) + '.</td><td><div class="table-avatar-cover"><img style="width: 60px;" src="/static/img/default.jpg" /></div></td><td>' + (res.rank[i]).realname + '</td><td>' + (res.rank[i]).userId + '</td><td>' + res.rank[i].accuracy + '%</td><td>' + hour + '' + min + '' + sec + '</td></tr>')
										}
									}
								}
							} else {
								$('table tbody').append('<tr><td>' + (+i + 1) + '.</td><td></td><td>' + (res.rank[i]).realname + '</td><td>' + (res.rank[i]).userId + '</td><td>' + res.rank[i].accuracy + '%</td><td>' + hour + '' + min + '' + sec + '</td></tr>');
							}

						} else {
							$('table tbody').append('<tr><td>' + (+i + 1) + '.</td><td></td><td></td><td></td><td></td><td></td></tr>')
						}
					}
				})
			})
		}
	}
</script>

<style scoped>
	#rank{
		position: relative;
		height: 100%;
		width: 100%;
	}
	#difficulty-choose {
		text-align: center;
		padding-top: 100px;
	}
	
	#difficulty-choose select {
		width: 300px;
		height: 40px;
		font-size: 24px;
		background: none;
		border: none;
		background-color: rgba(0, 0, 0, 0.3);
		color: white;
	}
	
	#difficulty-choose option {
		background: none;
	}
	.btn-back{
		position: absolute;
		left: 40px;
		top: 60px;
		font-size: 20px;
		background-color: rgba(0,0,0,0.5);
		color: white;
		border: none;
		padding: 5px 20px;
		border-radius: 8px;
		transition-duration: 0.2s;
		font-weight: bold;
		outline: none;
	}
	.btn-back:hover{
		background-color: rgba(0,0,0,0.9);
	}
</style>


